import { Component, OnInit, HostBinding, Output, EventEmitter, Input, ViewChild, TemplateRef } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { ActionOrganizationScope, ActionScope, OrganizationPotentialOwner, OrganizationLevel } from './actionOrganizationScope';

@Component({
  selector: 'app-organization-potential-owner',
  templateUrl: './organization-potential-owner.component.html',
  styleUrls: ['./organization-potential-owner.component.scss']
})
export class OrganizationPotentialOwnerComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';

  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input()
  set value(v: ActionOrganizationScope) {
    this.action = v.action;
    if (this.action === this.actionCollection[2].value) {
      if (v.enable === null || v.enable === undefined) {
        this.enable = true;
      } else {
        this.enable = v.enable;
      }
    } else {
      this.enable = v.enable;
    }
    if (v.scope) {
      this.organizationLevel = v.scope.organizationLevel;
      this.includeChildren = v.scope.includeChildren;
    }
  }

  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  modalConfig = {
    title: 'éé¡¹è®¾ç½®',
    width: 425,
    height: 360,
    showButtons: true
  };
  organizationLevelCollection = [{
    value: 'GLOBAL',
    text: 'å¨å',
    orgId: 'global',
  }, {
    value: 'UNIT',
    text: 'æ¬åä½',
    orgId: 'currentUnit'
  }, {
    value: 'DEPARTMENT',
    text: 'æ¬é¨é¨',
    orgId: 'currentDepartment'
    }];
  actionCollection = [{
      value: ActionScope.AddSignBehind, name: 'å ç­¾'
  }, {
      value: ActionScope.AddSignFront, name: 'å¾è¯¢'
  }, {
      value: ActionScope.Transfer, name: 'ç§»äº¤'
  }, {
    value: ActionScope.Notify, name: 'ç¥ä¼'
}];
  enable = false;
  organizationLevel = this.organizationLevelCollection[0].value;
  action = this.actionCollection[0].value;
  includeChildren = false;

  constructor() { }

  ngOnInit() {
  }

  clickOK() {
    const scope = new ActionOrganizationScope();
    scope.enable = this.enable;
    scope.action = this.action;
    if (scope.enable && this.organizationLevel !== this.organizationLevelCollection[0].value) {
      const potential = new OrganizationPotentialOwner();
      potential.actionScope = this.action;
      potential.organizationLevel = this.organizationLevel as OrganizationLevel;
      potential.organizationId = this.organizationLevelCollection.find(o => o.value === this.organizationLevel).orgId;
      potential.organizationName = this.organizationLevelCollection.find(o => o.value === this.organizationLevel).text;
      potential.includeChildren = this.includeChildren;
      potential.tag = 'variable';
      scope.scope = potential;
    }
    const object = { value: scope, parameters: null };
    this.submitModal.emit(object);
  }
  clickCancel() {
    this.closeModal.emit();
  }
  valueChange(event: any) {
    switch (event.value) {
      case this.organizationLevelCollection[0].value:
        this.includeChildren = false;
        break;
      case this.organizationLevelCollection[1].value:
        this.includeChildren = true;
        break;
      case this.organizationLevelCollection[2].value:
        this.includeChildren = false;
        break;
    }
  }
  actionName = () => {
    return this.actionCollection.find(a => a.value === this.action).name;
  }
}
export class OrganizationPotentialOwnerConverter implements TypeConverter {
  constructor() { }

  convertTo(data: ActionOrganizationScope): string {
    if (data.action === ActionScope.Transfer && (data.enable === null || data.enable === undefined)) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      if (data.enable) {
          return 'ï¼å·²è®¾ç½®ï¼';
      } else {
        return 'ï¼æªè®¾ç½®ï¼';
      }
    }
  }
}
