import { Component, OnInit, Output, Input, ViewChild, EventEmitter, TemplateRef, AfterViewInit } from '@angular/core';
import { DataTableComponent } from '@farris/ui-datatable';
import { TypeConverter } from '@farris/ui-property-panel';
import { HttpService } from '@ecp-caf/caf-common';
import { DatagridComponent } from '@farris/ui-datagrid';
import { MessagerService } from '@farris/ui-messager';

@Component({
  selector: 'app-entry-form-editor',
  templateUrl: './entry-form-editor.component.html',
  styleUrls: ['./entry-form-editor.component.css']
})
export class EntryFormEditorComponent implements OnInit, AfterViewInit {

  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input()
  set value(data: any) {
    if (data) {
      this.flowFormId = data.id;
    }
  }

  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;

  @ViewChild('grid') grid: DatagridComponent;

  @Input() editorParams: any = {};

  private flowFormId: string;
  modalConfig = {
    title: 'æµç¨åç±»',
    width: 540,
    height: 520,
    showButtons: true
  };

  columns = [
    { title: 'ç¼å·', field: 'code', width: 100, sortable: true },
    { title: 'åç§°', field: 'name', width: 100, sortable: true },
    { title: 'ç±»å', field: 'base', width: 60, formatter: { type: 'boolean', options: { trueText: 'åºç¡', falseText: 'æ©å±' } }}
  ];

  data = [];

  constructor(
    private http: HttpService,
    private message: MessagerService
  ) { }

  ngOnInit() {
    this.getBaseAndExtendFlowForm();
  }
  ngAfterViewInit() {
  }

  clickOK() {
    if (!this.grid.selectedRow) {
      this.message.info('å°æªéæ©æµç¨åç±»');
      return;
    }
    const object = { value: this.grid.selectedRow.data, parameters: null };
    this.submitModal.emit(object);
  }

  clickCancel() {
    this.closeModal.emit();
  }

  private getBaseAndExtendFlowForm() {
    if (this.flowFormId) {
      const url = `/api/runtime/wf/v1.0/flowForm/${this.flowFormId}/baseAndExtend`;
      this.http.get(url).subscribe((re: any) => {
        if (re && re.length > 0) {
          this.data = re;
          setTimeout(() => {
            this.grid.selectRow(this.flowFormId, true);
          }, 0);
        } else {
          this.message.info('æµç¨åç±»å·²ç»ä¸å­å¨');
        }
      });
    }
  }

}
export class FlowFormConverter implements TypeConverter {
  constructor() { }

  convertTo(data): string {
    if (data) {
      return data.name;
    }
  }
}




