import { Component, OnInit, Input, ViewChild, ElementRef, Injector, ComponentFactoryResolver, HostBinding, Output, EventEmitter } from '@angular/core';
import { ProcessTime } from './model/process-time';
import { DomSanitizer } from '@angular/platform-browser';
import { ProcessDesignerUIState } from './app/process-designer.uistate';
import { ResourceKeys } from './model/resourceKeys';
import { FlowFormInfo } from './domain/entities/flowform-info';
import { ProcessDesignerViewModel } from './app/process-designer.viewmodel';
import { BizComponentEntity } from './domain/entities/biz-component.entity';
import { ExecuteFormModel, TerminalType } from './views/execute-forms/entities/execute-form.entity';
import { ExcuteFormEntity } from './domain/entities/excute-form.entity';
import { ModalOptions, BsModalService } from '@farris/ui-modal';
import { BackWardRule } from './views/backward-rule/backward-rule.entity';
import { ProcessDesignerDataService } from './domain/process-designer.dataservice';
import { LoadingService } from '@farris/ui-loading';
import { NotifyService, NotifyOptions } from '@farris/ui-notify';
import { MessagerService } from '@farris/ui-messager';
import { WFFlowElementsViewModel } from './app/wf-flowElements.viewModel';
