import { Component, OnInit, HostBinding, Output, EventEmitter, Input, ViewChild, TemplateRef, ElementRef, ComponentFactoryResolver, Injector, AfterViewInit, AfterContentInit } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { of } from 'rxjs/observable/of';
import { FormBuilder } from '@angular/forms';
import { ExecuteFormsUIState } from './execute-forms.uistate';
import { ExecuteFormsViewModel } from './execute-forms.viewmodel';
import { ExecuteFormsRepository } from './execute-forms.repository';
import { ExecuteFormModel, TerminalType, ActualParameter } from './entities/execute-form.entity';
import { BsModalService } from '@farris/ui-modal';
import { AddExecuteFormComponent } from './add-execute-form/add-execute-form.component';
import { FormConfigContainerComponent } from './form-config-container/form-config-container.component';
import { ParameterSettingComponent } from './parameter-setting/parameter-setting.component';
import { Observable, Subject, forkJoin } from 'rxjs';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-execute-forms',
  templateUrl: './execute-forms.component.html',
  styleUrls: ['./execute-forms.component.css'],
  providers: [ExecuteFormsUIState, ExecuteFormsViewModel, ExecuteFormsRepository]
})
export class ExecuteFormsComponent implements OnInit, AfterViewInit {
  modalConfig = {
    title: 'è¡¨åå®ä¹',
    width: 794,
    height: 505,
    showButtons: true
  };

  @HostBinding('class')
  cls = 'farris-main-area flex-column';
  @HostBinding('style.background') style = '#FFF';
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input()
  set value(item: ExecuteFormModel) {
    if (item) {
      const paramArray = new Array<ActualParameter>();
      item.ActualParameters.forEach(a => {
        const para = new ActualParameter();
        para.Name = a.Name;
        para.Value = a.Value;
        paramArray.push(para);
      });
      this.form.ActualParameters = paramArray;
      this.form.Id = item.Id;
      this.form.Name = item.Name;
      this.form.ComponentId = item.ComponentId;
      this.form.formFormatId = item.formFormatId;
      this.form.formatConfigId = item.formatConfigId;
      this.form.FormType = item.FormType;
      this.form.TerminalType = item.TerminalType;
    }
  }
  @Input() editorParams: any = {};
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  @ViewChild('configContainer') configContainer: FormConfigContainerComponent;

  @ViewChild('configSection', { read: ElementRef }) configSection: ElementRef<any>;

  form: ExecuteFormModel;

  tab: 'web' | 'mobile' = 'web';

  groupIcon = '<i class="f-icon f-icon-lookup"></i>';

  cols = [];

  location: 'userActivity' | 'process' | 'startActivity';

  showMethodTab = true;

  // *******************************************************
  constructor(
    private formBuilder: FormBuilder,
    public uistate: ExecuteFormsUIState,
    public viewmodel: ExecuteFormsViewModel,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private modalService: BsModalService
  ) {
    this.form = new ExecuteFormModel();
  }
  ngAfterViewInit(): void {
    // this.editCSS();
  }

  ngOnInit() {
    this.location = this.editorParams.location;
    this.tab = this.editorParams.tab;
    if (this.location === 'userActivity') {
      this.showMethodTab = true;
    } else {
      this.showMethodTab = false;
    }
  }
  clickOK() {
    let data = new ExecuteFormModel();
    this.getForm().subscribe((re: any) => {
      if (!re.result) {
        return;
      }
      data = re.form;
      const object = { value: data, parameters: null };
      this.submitModal.emit(object);
    });
  }

  clickCancel() {
    this.closeModal.emit();
  }
  // tabChange(data) {
  //   this.tab = data.nextId;
  // }

  // editCSS() {
  //   this.configSection.nativeElement.querySelector('div.f-section-header').classList.add('mb-0');
  // }

  private getForm(): Observable<any> {
    if (this.form.ComponentId) {
      return of({ result: true, form: this.form });
    } else if (this.form.formFormatId) {
      return this.configContainer.submit().pipe(
        switchMap((re: any) => {
          if (re.result) {
            this.form.formatConfigId = re.configId;
            return of({ result: true, form: this.form });
          } else {
            return of({ result: false, form: this.form });
          }
        })
      );
    } else {
      return of({ result: true, form: null });
    }
  }

  /**
   * åæ°è®¾ç½®
   */
  parametersSet() {
    const compFactory = this.resolver.resolveComponentFactory(ParameterSettingComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.mode = 'bpmn';
    if (this.tab === 'web') {
      if (this.form.ComponentId) {
        compRef.instance.showFormType = true;
        compRef.instance.formType = this.form.FormType;
      }
      compRef.instance.parameters = [...this.form.ActualParameters.map(p => Object.assign({}, p))];
    } else {
      compRef.instance.parameters = [...this.form.ActualParameters.map(p => Object.assign({}, p))];
    }

    const modalConfig = {
      title: 'åæ°è®¾ç½®',
      width: 620,
      height: 400,
      showButtons: true,
      buttons: compRef.instance.modalFooter
    };
    const dialog = this.modalService.show(compRef, modalConfig);

    compRef.instance.submit.subscribe(re => {
      if (this.tab === 'web') {
        this.form.ActualParameters = re.parameters;
        this.form.FormType = re.formType;
      } else {
        this.form.ActualParameters = re.parameters;
      }
      dialog.close();
    });
    compRef.instance.cancel.subscribe(re => {
      dialog.close();
    });
  }
  /**
   * éæ©è¡¨åå®ä¹
   */
  selectExecuteForm() {
    const compFactory = this.resolver.resolveComponentFactory(AddExecuteFormComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.tab = this.tab;

    const modalConfig = {
      title: 'è¡¨åå®ä¹åè¡¨',
      width: 650,
      height: 400,
      showButtons: true,
      buttons: compRef.instance.modalFooter
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.submit.subscribe((re: ExecuteFormModel) => {
      this.form = re;
      dialog.close();
    });
    compRef.instance.cancel.subscribe(re => {
      dialog.close();
    });
  }
  clear() {
    this.form = new ExecuteFormModel();
  }
}
export class ExecuteFormsConverter implements TypeConverter {
  constructor() { }

  convertTo(data): string {
    if (data) {
      return data.Name;
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
