import { Component, OnInit, HostBinding, ViewChild, Injector, NgZone, AfterViewInit, ElementRef } from '@angular/core';
import { SysFarrisLookupComponent } from '@gsp-sys/sysmgr-ui';
import { ProcessDesignerUIState } from '../../../../app/process-designer.uistate';
import { ExprEditMode, ExpressionService, InjectedType } from '@gsp-svc/expression';
import { UserPerformerEntity } from '../../entities/user-performer.entity';
import { PerformerConfigService } from '../../performer-config.service';
import { HttpService } from '@ecp-caf/caf-common';
import {MessagerService} from "@farris/ui-messager";

declare var System: any;
@Component({
  selector: 'app-user-performer',
  templateUrl: './user-performer.component.html',
  styleUrls: ['./user-performer.component.scss']
})
export class UserPerformerComponent implements OnInit, AfterViewInit {
  @HostBinding('class')
  cls = 'd-flex flex-fill h-100';

  @ViewChild(SysFarrisLookupComponent) userHelp: SysFarrisLookupComponent;

  @ViewChild('inputGroup') inputGroup: ElementRef;
  showClearButton = false;

  bindingData: { id?, name?} = {};

  userTypeCollection = [
    { text: 'åºå®ç¨æ·', value: 'user' },
    { text: 'åéåä¸è', value: 'variable' }
  ];
  userType = this.userTypeCollection[0].value;
  variableOrgTypes = [
    { name: 'ç³»ç»ç¨æ·', value: 'Sys' },
    { name: 'è¡æ¿äººå', value: 'Administrative_organization' }
  ];
  curOrgType = this.variableOrgTypes[0].value;
  hasAdminOrg = false;
  userTypeButtonNeedShow = false; // ç¨æ·ç±»ååéæé®æ¯å¦éè¦æ¾ç¤º

  condition: string;
  users: UserPerformerEntity[] = [];
  constructor(
    private designerUIState: ProcessDesignerUIState,
    private expr: ExpressionService,
    private injector: Injector,
    private lookupConfig: PerformerConfigService,
    private ngZone: NgZone,
    private http?: HttpService,
    private message?: MessagerService,
  ) { }

  ngOnInit() {
    let url = '/api/runtime/wf/v1.0/reportingOrgType/hasAdminOrg';
    this.http.get(url).subscribe((data: any) => {
      this.hasAdminOrg = data;
    });
  }

  ngAfterViewInit(): void {
    this.ngZone.runOutsideAngular(() => {
      this.inputGroup.nativeElement.addEventListener(
        'mouseenter',
        this.onMouseEnter.bind(this)
      );
      this.inputGroup.nativeElement.addEventListener(
        'mouseleave',
        this.onMouseLeave.bind(this)
      );
    });
  }

  onMouseEnter(event) {
    this.showClearButton = true;
    if (this.showClearButton) {
      this.toggleClearIcon(event, true);
    }
  }
  onMouseLeave(event) {
    this.showClearButton = false;
    this.toggleClearIcon(event, false);
  }
  private toggleClearIcon(event: any, isShow = false) {
    const str = isShow ? '' : 'none';
    const clearIcon = event.target.querySelector('.input-group-clear');
    if (clearIcon) {
      clearIcon.style.display = str;
    }
  }
  userTypeChange(data) {
    // åæ¢ç±»åæ¶ï¼æ¸ç©ºusers
    this.users = [];
    // åæ¢ç±»åæ¶ï¼äººåç±»åèªå¨åæ¢æç³»ç»ç¨æ·
    this.orgTypeChange('Sys');
    // æè¡æ¿å¸®å©å¹¶ä¸åæ¢å°åéåä¸èææ¾ç¤º
    this.userTypeButtonNeedShow = this.hasAdminOrg && data.data.value === 'variable';
  }
  clear(data: any) {
    this.bindingData = {};
    this.users = [];
    this.variableOrgTypes = [
      { name: 'ç³»ç»ç¨æ·', value: 'Sys' },
      { name: 'è¡æ¿äººå', value: 'Administrative_organization' }
    ];
  }
  selectUsers(data: any) {
    // åºå®ç¨æ·
    if (this.userType === this.userTypeCollection[0].value) {
      this.lookupConfig.getLookupConfig().subscribe(re => {
        // é¡¹ç®éç½®äºèªå·±çå¸®å©æ¶
        if (re.user && re.user.impl) {
          System.import(re.user.impl)
            .then((moduleFactorys: any) => {
              const moduleFactory: any = Object.values(moduleFactorys)[0];
              const moduleRef = moduleFactory.create(this.injector);
              moduleRef.instance.showHelp().subscribe(data => {
                if (data && data.length > 0) {
                  this.users = data.map(u => {
                    const user = new UserPerformerEntity(u.id, u.name, 'wf.UserParticipant');
                    user.orgId = u.orgId;
                    user.orgName = u.orgName;
                    user.userType = this.userTypeCollection[0].value;
                    return user;
                  });
                }
              });
            });
        } else { // å è½½é»è®¤å¸®å©
          this.userHelp.showHelp();
        }
      });
    } else {
      this.showExprHelp();
    }
  }
  editConditon() {
    if (this.designerUIState.schemas.length > 0) {
      for (const schema of this.designerUIState.schemas) {
        this.expr.addSchema('Schema', schema);
      }
      if (this.designerUIState.schemas[0].entityTypes && this.designerUIState.schemas[0].entityTypes.length > 0) {
        const entityType = this.designerUIState.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array();
    if (this.designerUIState.procInstContext.length > 0) {
      for (const variable of this.designerUIState.procInstContext) {
        context.push(variable);
      }
    }
    if (this.designerUIState.formalParameterContext.length > 0) {
      for (const variable of this.designerUIState.formalParameterContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.designerUIState.participantContext.length > 0) {
      for (const variable of this.designerUIState.participantContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.designerUIState.processVariableContext.length > 0) {
      for (const variable of this.designerUIState.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.designerUIState.activityComponentContext.length > 0) {
      for (const variable of this.designerUIState.activityComponentContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
    this.expr.setEditPattern(ExprEditMode.Conditional); // è¡¨è¾¾å¼å±ç°æ¨¡å¼ï¼é»è®¤ç®æç
    this.expr.addExpressionText(this.condition);
    this.expr.buildExpression().then((expressioninfo: string) => {
      this.condition = expressioninfo;
    });
  }
  usersSelect(data: any[]) {
    this.users = data.map(u => {
      const user = new UserPerformerEntity(u.id, u.name, 'wf.UserParticipant');
      user.orgId = u.sysOrgId;
      user.orgName = u.sysOrgName;
      user.userType = this.userTypeCollection[0].value;

      return user;
    });
  }
  showExprHelp() {
    if (this.designerUIState.schemas.length > 0) {
      for (const schema of this.designerUIState.schemas) {
        this.expr.addSchema('Schema', schema);
      }
      if (this.designerUIState.schemas[0].entityTypes && this.designerUIState.schemas[0].entityTypes.length > 0) {
        const entityType = this.designerUIState.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array();
    if (this.designerUIState.participantContext.length > 0) {
      for (const variable of this.designerUIState.participantContext) {
        context.push(variable);
      }
    }
    if (this.designerUIState.processVariableContext.length > 0) {
      for (const variable of this.designerUIState.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
    if (this.users.length > 0) {
      this.expr.addExpressionText(this.users[0].performerId);
    } else {
      this.expr.addExpressionText('');
    }
    this.expr.buildExpression().then((expressioninfo: string) => {
      const user = new UserPerformerEntity(expressioninfo, '', 'wf.UserParticipant');
      user.tag = 'expression';
      user.performerName = this.variableParticipantName(expressioninfo);
      user.userType = this.userTypeCollection[1].value;
      if(this.variableParticipantName(expressioninfo) === ''){
        this.message.error('è¡æ¿äººåç±»åä¸è½ä½¿ç¨ä¸ä¸æåé');
        this.users = [];
      }else{
        this.users = [user];
      }
      if(user.performerId.indexOf("GetContextParameter") >= 0){ // æ ¹æ®è¡¨è¾¾å¼ä¸­çå½æ°åå¤æ­æ¯å¦ä½¿ç¨çæ¯ä¸ä¸æåé
        this.variableOrgTypes = [
          { name: 'ç³»ç»ç¨æ·', value: 'Sys' }
        ];
      } else {
        this.variableOrgTypes = [
          { name: 'ç³»ç»ç¨æ·', value: 'Sys' },
          { name: 'è¡æ¿äººå', value: 'Administrative_organization' }
        ];
      }
    });
  }
  getPerformers() {
    this.users.map(user => {
      if (user.orgId === undefined) {
        user.orgType = this.curOrgType;
        return user;
      }
    });
    if (this.condition) {
      this.users = this.users.map(user => {
        user.condition = this.condition;
        return user;
      });
    }
    return this.users;
  }
  private variableParticipantName(expr: string): string {
    for (const c of this.designerUIState.participantContext) {
      if (expr.indexOf(c.key) > -1) {
        if(this.curOrgType !== 'Sys'){ // è¡æ¿äººåç±»åä¸åè®¸ä½¿ç¨ä¸ä¸æåé
          return '';
        }
        return c.name;
      }
    }
    for (const c of this.designerUIState.processVariableContext) {
      if (expr.indexOf(c.key) > -1) {
        return c.name;
      }
    }
    return 'ä¸å¡å­æ®µ';
  }
  orgTypeChange(data: any) {
    if (data === 'Sys') {
      this.curOrgType = this.variableOrgTypes[0].value;
    } else {
        this.curOrgType = this.variableOrgTypes[1].value;
    }
  }
}
