import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { GSPViewModel } from '@gsp-bef/gsp-vo-metadata';
import { CommonModelUtil } from '@gsp-bef/gsp-cm-metadata';
import { VOTemplateType } from '../entity/VOTemplateType';

export class CommonModelTypeHelper {
    public static getCommonModelType(cm: GSPBusinessEntity | GSPViewModel): string {
        let type = CommonModelUtil.GetGeneratedConfigID(cm as any);
        type = "com." + type;
        return type;
    }

    /**
     * å¤æ­VOæ¥æºäºBEè¿æ¯VOæèå®å¨èæ
     * @param gspViewModel
     */
    public static judgeVoTemplateType(gspViewModel: GSPViewModel): VOTemplateType {
        if (gspViewModel.IsVirtual === true) {
            if (gspViewModel.TemplateVoInfo && gspViewModel.TemplateVoInfo.TemplateVoId != null) {
                return VOTemplateType.FromVO;
            } else {
                return VOTemplateType.FromNone
            }
        } else {
            return VOTemplateType.FromBE;
        }
    }
}
