import { Component, OnInit, ViewChild, TemplateRef, Output, EventEmitter } from '@angular/core';
import { ProcessDesignerUIState } from '../../../../app/process-designer.uistate';
import { DatagridComponent } from '@farris/ui-datagrid';
import { CustomizeAciton } from '../../entities/customize-action.entity';
import { ActualParameter } from '../../../execute-forms/entities/execute-form.entity';

@Component({
  selector: 'app-customize-action-list',
  templateUrl: './customize-action-list.component.html',
  styleUrls: ['./customize-action-list.component.scss']
})
export class CustomizeActionListComponent implements OnInit {

  @ViewChild('grid') grid: DatagridComponent;
  @ViewChild('footer') footer: TemplateRef<any>;

  @Output() submit = new EventEmitter<any[]>();
  @Output() cancel = new EventEmitter<any>();

  customActionList: any[];

  private terminalList = [
    { value: 'app', text: 'ç§»å¨' },
    { value: 'web', text: 'web' },
    { value: 'web,app', text: 'web,ç§»å¨' }
  ];

  cols = [
    { field: 'code', width: 100, title: 'ç¼å·' },
    { field: 'name', width: 100, title: 'åç§°' },
    {
      field: 'terminal', width: 100, title: 'éç¨ç»ç«¯',
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: this.terminalList }
      }, },
    {
      field: 'isAll', width: 100, title: 'æ¯å¦é»è®¤',
      formatter: { type: 'boolean', options: { trueText: 'æ¯', falseText: 'å¦' } }  }
  ];
  constructor(private designerUIState: ProcessDesignerUIState) {
    this.customActionList = this.designerUIState.flowFormInfo.customizeActions || [];
  }

  ngOnInit() {
  }
  clickOK() {
    const selections = [];
    const checkeds: any[] = this.grid.checkeds;
    if (checkeds && checkeds.length > 0) {
      checkeds.forEach(action => {
        const item = new CustomizeAciton(action.data.id, action.data.code, action.data.name);
        item.terminalType = action.data.terminal;
        // item.enable = true;
        item.actualParameters = [];
        if (action.data.actualParameters && action.data.actualParameters.length > 0) {
          for (const p of action.data.actualParameters) {
            const param = {
              Name: p.code,
              Value: p.value,
              ValueText: p.name,
              Tag: 'expression'
            } as ActualParameter;
            item.actualParameters.push(param);
          }
        }
        selections.push(item);
      });
    }
    this.submit.emit(selections);
  }
  clickCancel() {
    this.cancel.emit(true);
  }
}
