import { Component, Input, OnInit, Output, ViewChild, TemplateRef, EventEmitter } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';
import { ProcExtendProperty } from './extend-property';

@Component({
  selector: 'lib-extend-property',
  templateUrl: './extend-property.component.html',
  styleUrls: ['./extend-property.component.css']
})
export class ExtendPropertyComponent implements OnInit {

  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;

  @Input()
  set value(property: any[]) {
    if (property && property.length > 0) {
      this.procExtendProperty = property.map(p => ({
        id: p.id,
        code: p.code,
        parentId: p.parentId,
        sortOrder: p.sortOrder,
        propertyNote: p.propertyNote,
        propertyTitle: p.propertyTitle,
        propertyLocation: p.propertyLocation,
        propertyType: p.propertyType,
        enumValue: p.enumValue,
        bizDefKey: p.bizDefKey,
        bizCategoryId: p.bizCategoryId,
        startMode: p.startMode,
        value: p.value,
      }));
    }
  }
  modalConfig = {
    title: 'æ©å±å±æ§',
    width: 430,
    height: 370,
    showButtons: true
  };
  procExtendProperty: Array<ProcExtendProperty> = new Array();
  booleanlist = [
    { key: 'æ¯', value: 'true' },
    { key: 'å¦', value: 'false' }
  ];
  extendPropertyList = {
    key: '',
    value: ''
  };
  constructor(private uiState: ProcessDesignerUIState) {
  }

  setProperty(obj: any) {
    this.uiState.propertyData = obj.propertyData;
    this.uiState.propertyConfig = obj.propertyConfig;
  }

  ngOnInit() {
  }

  clickOK() {
    const value = this.procExtendProperty;
    const object = { value, parameters: null };
    this.submitModal.emit(object);
  }

  cancel() {
    this.closeModal.emit();
  }
}
