import { Component, OnInit, Output, Input, ViewChild, EventEmitter, TemplateRef, HostBinding } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { FormGroup, FormControl } from '@angular/forms';
import { AssignPerformerConfig } from './assign-performer-config.entity';
import { OrganizationPotentialOwner, ActionScope, OrganizationLevel } from '../organization-potential-owner/actionOrganizationScope';

@Component({
  selector: 'app-assign-performer-config',
  templateUrl: './assign-performer-config.component.html',
  styleUrls: ['./assign-performer-config.component.css']
})
export class AssignPerformerConfigComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();
  assingPerformerConfigForm: FormGroup;
  private config: AssignPerformerConfig;

  @Input()
  set value(v: AssignPerformerConfig) {
    this.assingPerformerConfigForm = new FormGroup({
      multiSelect: new FormControl(v.multiSelect),
      necessary: new FormControl(v.necessary)
    });
    this.operator = v.operator || 'currentUser';
    this.allowGlobalAssign = v.allowGlobalAssign || false;
    if (v.assignScope) {
      this.organizationLevel = v.assignScope.organizationLevel;
      this.includeChildren = v.assignScope.includeChildren;
    }
  }

  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;

  allowGlobalAssign = false;

  operatorCollection = [
    { text: 'å½ååçäºº', value: 'currentUser' },
    { text: 'æµç¨åèµ·äºº', value: 'procInstCreator' }];
  operator = 'currentUser';

  organizationLevelCollection = [
    { value: 'GLOBAL', text: 'å¨å', orgId: 'global' },
    { value: 'UNIT', text: 'æ¬åä½', orgId: 'currentUnit' },
    { value: 'DEPARTMENT', text: 'æ¬é¨é¨', orgId: 'currentDepartment' }];
  organizationLevel = this.organizationLevelCollection[0].value;
  includeChildren = false;

  modalConfig = {
    title: 'ææ´¾è§å',
    width: 450,
    height: 470,
    showButtons: true
  };
  constructor() { }

  ngOnInit() {
  }

  clickOK() {
    this.config = {
      operator: this.operator,
      multiSelect: this.assingPerformerConfigForm.get('multiSelect').value,
      necessary: this.assingPerformerConfigForm.get('necessary').value,
      allowGlobalAssign: this.allowGlobalAssign
    };
    if (this.allowGlobalAssign && this.organizationLevel !== this.organizationLevelCollection[0].value) {
      const potential = new OrganizationPotentialOwner();
      potential.actionScope = ActionScope.AssignUser;
      potential.organizationLevel = this.organizationLevel as OrganizationLevel;
      potential.organizationId = this.organizationLevelCollection.find(o => o.value === this.organizationLevel).orgId;
      potential.organizationName = this.organizationLevelCollection.find(o => o.value === this.organizationLevel).text;
      potential.includeChildren = this.includeChildren;
      potential.tag = 'variable';
      this.config.assignScope = potential;
    }
    const object = { value: this.config, parameters: null };
    this.submitModal.emit(object);
  }

  clickCancel() {
    this.closeModal.emit();
  }

  valueChange(event: any) {
    switch (event.value) {
      case this.organizationLevelCollection[0].value:
        this.includeChildren = false;
        break;
      case this.organizationLevelCollection[1].value:
        this.includeChildren = true;
        break;
      case this.organizationLevelCollection[2].value:
        this.includeChildren = false;
        break;
    }
  }
}
export class AssignConfigConverter implements TypeConverter {
  constructor() { }

  convertTo(data): string {
    return 'ï¼å·²è®¾ç½®ï¼';
  }
}
