import { Component, OnInit, Input, ViewChild, TemplateRef, ComponentFactoryResolver, Injector } from '@angular/core';
import { EditorTypes } from '@farris/ui-datagrid-editors';
import { workFlowActions, executionTime, FormMethodConfig } from '../entities/form-method-config';
import { HttpService } from '@ecp-caf/caf-common';
import { FormMethod } from '../entities/form-method';
import { DialogComponent } from '@farris/ui-dialog';
import { DatagridComponent } from '@farris/ui-datagrid';
import { MessagerService } from '@farris/ui-messager';
import { BsModalService } from '@farris/ui-modal';
import { ParameterSettingComponent } from '../parameter-setting/parameter-setting.component';

@Component({
  selector: 'lib-form-operation-config',
  templateUrl: './form-operation-config.component.html',
  styleUrls: ['./form-operation-config.component.scss']
})
export class FormOperationConfigComponent implements OnInit {
  private _fromFormatId: string;
  @Input()
  set formFormatId(v: string) {
    this._fromFormatId = v;
    if (this._fromFormatId) {
      this.getFormMethods();
    }
  }
  get formFormatId() {
    return this._fromFormatId;
  }

  @Input() formFormatConfigId: string;
  @Input() operations = new Array<FormMethodConfig>();

  @ViewChild('methodsDialog') methodsDialog: DialogComponent;
  @ViewChild('grid') grid: DatagridComponent;
  @ViewChild('methodGrid') methodGrid: DatagridComponent;
  @ViewChild('cell') cell: TemplateRef<any>;

  cols = new Array();

  methodCols = [
    { field: 'method', width: 100, title: 'æ¹æ³æ è¯', showTips: true },
    { field: 'methodName', width: 80, title: 'æ¹æ³åç§°', showTips: true },
    { field: 'description', width: 120, title: 'æè¿°', showTips: true }
  ];
  e: any;

  formMethods = new Array<FormMethod>();
  state: 'add' | 'edit' = 'add';

  constructor(
    private http: HttpService,
    private message: MessagerService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private modalService: BsModalService
  ) { }

  ngOnInit() {
    this.cols = [
    {
      field: 'workFlowAction', width: 80, title: 'æµè½¬æ¹å',
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: workFlowActions }
      },
      editor: {
        type: EditorTypes.COMBOLIST,
        options: { valueField: 'value', textField: 'text', data: workFlowActions, idField: 'value', panelHeight: 'auto', enableClear: false }
      },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    }, {
      field: 'method', width: 100, title: 'è¡¨åæä½',
      editor: {
        type: 'input-group',
        options: {
          groupText: '<i class="f-icon f-icon-lookup"></i>',
          editable: false,
          clickHandle: (e) => {
            e.formControl.setValue(e.instance.value ? e.instance.value : '');
            this.e = e;
            this.methodsDialog.show();
            this.state = 'edit';
          },
          clear: (e) => {
            console.log('clear', e);
          }
        }
      },
      formatter: (v, d, i) => {
        if (v) {
          const m = this.formMethods.find(item => item.method === v);
          if (m) {
            return m.methodName;
          } else {
            return '';
          }
        } else {
          return '';
        }
      },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    },
    {
      field: 'executionTime', width: 80, title: 'æ§è¡æ¶æº',
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: executionTime }
      },
      editor: {
        type: EditorTypes.COMBOLIST,
        options: { valueField: 'value', textField: 'text', data: executionTime, idField: 'value', panelHeight: 'auto', enableClear: false }
      },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    },
    {
      field: 'sortOrder', width: 60, title: 'æ§è¡é¡ºåº',
      editor: {
        type: EditorTypes.NUMBERBOX, options: {
        }
      },
      formatter: { type: 'number', options: { precision: 0 } },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    },
    {
      field: 'methodParameters', width: 80, title: 'åæ°',
      template: this.cell,
      halign: 'center', align: 'center'
    }
  ];
  }

  add() {
    this.methodsDialog.show();
    this.state = 'add';
  }

  delete() {
    if (this.grid.selectedRow) {
      this.operations = this.operations.filter(o => o.id !== this.grid.selectedRow.id);
    } else {
      this.message.info('å°æªéä¸­è¡¨åæä½éç½®ï¼');
    }
  }

  editParam(ctx: any) {
    const compFactory = this.resolver.resolveComponentFactory(ParameterSettingComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.mode = 'usual';
    compRef.instance.showFormType = false;
    if (ctx.rowData.methodParameters) {
      compRef.instance.parameters = [...ctx.rowData.methodParameters.map(p => Object.assign({}, p))];
    } else {
      compRef.instance.parameters = new Array();
    }

    const modalConfig = {
      title: 'åæ°è®¾ç½®',
      width: 620,
      height: 400,
      showButtons: true,
      buttons: compRef.instance.modalFooter
    };
    const dialog = this.modalService.show(compRef, modalConfig);

    compRef.instance.submit.subscribe(re => {
      for (const o of this.operations) {
        if (o.id === ctx.rowData.id) {
          o.methodParameters = re.parameters;
        }
      }
      dialog.close();
    });
    compRef.instance.cancel.subscribe(re => {
      dialog.close();
    });
  }

  methodSelected() {
    if (this.methodGrid.selectedRow) {
      const data = this.methodGrid.selectedRow.data;
      if (this.state === 'edit') {
        if (this.e) {
          this.e.formControl.setValue(data.method);
          this.e.instance.cd.detectChanges();
          for (const o of this.operations) {
            if (o.id === data.id) {
              o.method = data.method;
              o.sortOrder = data.sortOrder;
              o.methodParameters = data.methodParameters;
            }
          }
        }
      } else {
        const operation = new FormMethodConfig();
        operation.formFormatConfigId = this.formFormatConfigId;
        operation.method = data.method;
        operation.sortOrder = data.sortOrder;
        operation.methodParameters = data.methodParameters;
        this.operations = this.operations.concat([operation]);
      }
      this.methodsDialog.close();
    } else {
      this.message.info('å°æªéä¸­è¡¨åæä½ï¼');
    }
  }

  validator(): boolean {
    this.grid.endCellEdit();
    for (const o of this.operations) {
      if (!o.method) {
        this.message.info('å°æªéæ©è¡¨åæä½ï¼');
        return false;
      }
    }
    return true;
  }

  private getFormMethods() {
    if (this.formFormatId) {
      const url = `/api/runtime/wf/v1.0/formOperation?formFormatId=${this.formFormatId}`;
      this.http.get(url).subscribe((re: any) => {
        if (re) {
          this.formMethods = re;
        }
      });
    }
  }

}
