    getValue(data: any, code: string): string {
        if (!code) {
            return '';
        }
        let resultVal = '';
        if (code.indexOf('.') === -1) {
            resultVal = data[code];
        } else {
            resultVal = code.split('.').reduce((obj, key) => {
                if (obj) {
                    return obj[key];
                } else {
                    return null;
                }
            }, data);
        }
        return resultVal;
    }
    /**
     * è½¬æ¢
     * @param jsonObj Object
     */
    toUpperCase(jsonObj: Object): any {
        if (typeof (jsonObj) === 'object') {
            for (const key of Object.keys(jsonObj)) {
                jsonObj[key.substring(0, 1).toUpperCase() + key.substring(1)] = jsonObj[key];
                delete (jsonObj[key]);
            }
        }
        return jsonObj;
    }
    toLowCase(jsonObj: Object): any {
        if (typeof (jsonObj) === 'object') {
            for (const key of Object.keys(jsonObj)) {
                if ((key.substring(0, 1).toLowerCase() + key.substring(1)) !== key) {
                    jsonObj[key.substring(0, 1).toLowerCase() + key.substring(1)] = jsonObj[key];
                    delete (jsonObj[key]);
                }
            }
        }
        return jsonObj;
    }
