import { Component, OnInit, TemplateRef, ViewChild, HostBinding, Input } from '@angular/core';
import { HttpService } from '@ecp-caf/caf-common';
import { IFormFormatConfigComponent } from "@gsp-wf/biz-process-api";
import { Observable, of, forkJoin, Subject } from 'rxjs';
import { FormFormat, FormFieldData, FormField } from '../entities/form-format';
import { FormFormatConfig, FormButtonConfig, FormFieldConfig } from '../entities/form-format-config';
import { DatagridComponent } from '@farris/ui-datagrid';
import { ProcessDeUtil } from '../../../domain/process-de-util';
import { MessagerService } from '@farris/ui-messager';
import { NotifyService } from '@farris/ui-notify';
import { PickedResult, ServerSideToken, LookupGridComponent } from '@farris/ui-lookup';
import { switchMap, catchError } from 'rxjs/operators';
import { FormOperationConfigComponent } from '../form-operation-config/form-operation-config.component';
import { TreeTableComponent } from '@farris/ui-treetable';

@Component({
  selector: 'lib-form-format-config',
  templateUrl: './form-format-config.component.html',
  styleUrls: ['./form-format-config.component.scss']
})
export class FormFormatConfigComponent implements OnInit, IFormFormatConfigComponent {

  @ViewChild('btnEnable') btnEnable: TemplateRef<any>;
  @ViewChild('btnDisabled') btnDisabled: TemplateRef<any>;
  @ViewChild('btnHidden') btnHidden: TemplateRef<any>;
  @ViewChild('fieldEditable') fieldEditable: TemplateRef<any>;
  @ViewChild('fieldReadonly') fieldReadonly: TemplateRef<any>;
  @ViewChild('fieldHidden') fieldHidden: TemplateRef<any>;
  @ViewChild('fieldRequired') fieldRequired: TemplateRef<any>;

  @ViewChild('btnGrid') btnGrid: DatagridComponent;
  @ViewChild('fieldTree') fieldTree: TreeTableComponent;
  @ViewChild(FormOperationConfigComponent) operationConfig: FormOperationConfigComponent;

  config: FormFormatConfig = new FormFormatConfig();
  formFormat: FormFormat = new FormFormat();

  state: 'new' | 'edit' = 'edit';

  @Input() showMethodTab = true;

  private _formFormatId: string;
  set formFormatId(v: string) {
    this._formFormatId = v;
    this.config.formFormatId = v;
  }

  get formFormatId() {
    return this._formFormatId;
  }

  private _formatConfigId: string;
  set formatConfigId(v: string) {
    this._formatConfigId = v;
    if (!v) {
      this.state = 'new';
    }
  }
  get formatConfigId() {
    return this._formatConfigId;
  }

  buttonCols = [];
  buttonConfigs = new Array();

  fieldCols = [];
  fieldConfigs = new Array();

  formatFieldList: Array<FormField> = new Array();

  constructor(
    private http: HttpService,
    private message: MessagerService,
    private notify: NotifyService
  ) { }

  ngOnInit() {
    this.buttonCols = [
      { field: 'buttonName', width: 100, title: 'æé®åç§°' },
      // { field: 'buttonId', width: 100, title: 'æé®æ è¯' },
      {
        title: 'æ¯å¦å¯ç¨',
        field: 'enable',
        width: 80,
        template: this.btnEnable,
        align: 'center',
        hAlign: 'center'
      },
      {
        title: 'æ¯å¦ç¦ç¨',
        field: 'disabled',
        width: 80,
        template: this.btnDisabled,
        align: 'center',
        hAlign: 'center'
      },
      {
        title: 'æ¯å¦éè',
        field: 'hidden',
        width: 80,
        template: this.btnHidden,
        align: 'center',
        hAlign: 'center'
      }];
    this.fieldCols = [
      { field: 'fieldName', width: 100, title: 'å­æ®µåç§°' },
      // { field: 'fieldId', width: 100, title: 'å­æ®µæ è¯' },
      {
        title: 'æ¯å¦å¯å',
        field: 'editable',
        width: 80,
        template: this.fieldEditable,
        align: 'center',
        hAlign: 'center'
      },
      {
        title: 'æ¯å¦åªè¯»',
        field: 'readonly',
        width: 80,
        template: this.fieldReadonly,
        align: 'center',
        hAlign: 'center'
      },
      {
        title: 'æ¯å¦éè',
        field: 'hidden',
        width: 80,
        template: this.fieldHidden,
        align: 'center',
        hAlign: 'center'
      },
      {
        title: 'æ¯å¦å¿å¡«',
        field: 'required',
        width: 80,
        template: this.fieldRequired,
        align: 'center',
        hAlign: 'center'
      }];
    this.getConfig();
  }

  submit(): Observable<any> {
    if (this.validator()) {
      this.setBtnAndFieldConfigs();
      if (this.state === 'new' &&
        this.config.formButtonConfigs.length === 0 &&
        this.config.formFieldConfigs.length === 0 &&
        this.operationConfig.operations.length === 0
      ) {
        return of({ result: true, configId: null });
      } else {
        return this.saveConfig(this.config).pipe(
          switchMap(re => {
            return of({ result: true, configId: this.config.id });
          }),
          catchError(re => {
            this.message.error('ä¿å­éç½®å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
            return of({ result: false, configId: this.config.id });
          })
        );
      }
    } else {
      return of({ result: false, configId: null });
    }
  }

  btnConfigChange(bool: boolean, field: string, id: string) {
    if (bool) {
      for (const b of this.buttonConfigs) {
        if (b.id === id) {
          switch (field) {
            case 'enable':
              b.enable = bool;
              b.disabled = false;
              b.hidden = false;
              break;
            case 'disabled':
              b.disabled = bool;
              b.enable = false;
              b.hidden = false;
              break;
            case 'hidden':
              b.hidden = bool;
              b.disabled = false;
              b.enable = false;
              break;
          }
        }
      }
    }
  }

  btnBatchOperate(operation: string) {
    const checkeds = this.btnGrid.checkedRows;
    if (checkeds && checkeds.length > 0) {
      for (const c of checkeds) {
        this.btnConfigChange(true, operation, c.id);
      }
    }
  }

  btnBatchReset() {
    const checkeds = this.btnGrid.checkedRows;
    if (checkeds && checkeds.length > 0) {
      for (const c of checkeds) {
        const button = this.formFormat.formButtons.find(b => b.id === c.id);
        for (const b of this.buttonConfigs) {
          if (b.id === c.id) {
            b.enable = button.buttonState === 'Enable' ? true : false;
            b.disabled = button.buttonState === 'Disabled' ? true : false;
            b.hidden = button.buttonState === 'Hidden' ? true : false;
          }
        }
      }
    }
  }

  fieldConfigChange(bool: boolean, field: string, id: string) {
    if (bool) {
      this.fieldSwitchChange(this.fieldConfigs, field, id);
    }
  }
  private fieldSwitchChange(fieldConfigs: Array<any>, field: string, id: string) {
    if (fieldConfigs.length > 0) {
      for (const f of fieldConfigs) {
        if (f.data.id === id) {
          switch (field) {
            case 'editable':
              f.data.editable = true;
              f.data.readonly = false;
              f.data.hidden = false;
              f.data.required = false;
              break;
            case 'readonly':
              f.data.editable = false;
              f.data.readonly = true;
              f.data.hidden = false;
              f.data.required = false;
              break;
            case 'hidden':
              f.data.editable = false;
              f.data.readonly = false;
              f.data.hidden = true;
              f.data.required = false;
              break;
            case 'required':
              f.data.editable = false;
              f.data.readonly = false;
              f.data.hidden = false;
              f.data.required = true;
              break;
          }
          return;
        } else {
          if (f.children && f.children.length > 0) {
            this.fieldSwitchChange(f.children, field, id);
          }
        }
      }
    }
  }

  fieldBatchOperate(operation: string) {
    const checkeds = this.fieldTree.checkeds;
    if (checkeds && checkeds.length > 0) {
      for (const c of checkeds) {
        this.fieldConfigChange(true, operation, c.data.id);
      }
    }
  }

  fieldBatchReset() {
    const checkeds = this.fieldTree.checkeds;
    if (checkeds && checkeds.length > 0) {
      for (const c of checkeds) {
        const field = this.formatFieldList.find(f => f.id === c.data.id);
        this.resetFieldConfig(this.fieldConfigs, c.data.id, field);
      }
    }
  }
  resetFieldConfig(fieldConfigs: Array<any>, id: string, field: FormField) {
    if (fieldConfigs.length > 0) {
      for (const f of fieldConfigs) {
        if (f.leaf === true && f.data.id === id) {
          f.data.editable = field.fieldState === 'Editable' ? true : false;
          f.data.readonly = field.fieldState === 'Readonly' ? true : false;
          f.data.hidden = field.fieldState === 'Hidden' ? true : false;
          f.data.required = field.fieldState === 'Required' ? true : false;
          return;
        } else {
          if (f.children && f.children.length > 0) {
            this.resetFieldConfig(f.children, id, field);
          }
        }
      }
    }
  }

  private validator(): boolean {
    return this.operationConfig.validator();
  }


  private setBtnAndFieldConfigs() {
    this.config.formButtonConfigs = new Array<FormButtonConfig>();
    for (const btn of this.buttonConfigs) {
      const button = this.formFormat.formButtons.find(b => b.buttonId === btn.buttonId);
      const btnConfig = new FormButtonConfig();
      btnConfig.buttonId = btn.buttonId;
      if (btn.enable) {
        btnConfig.buttonState = 'Enable';
      } else if (btn.disabled) {
        btnConfig.buttonState = 'Disabled';
      } else if (btn.hidden) {
        btnConfig.buttonState = 'Hidden';
      } else {
        continue;
      }
      if (!button.buttonState && btnConfig.buttonState) {
        this.config.formButtonConfigs.push(btnConfig);
      }
      if (button.buttonState && button.buttonState !== btnConfig.buttonState) {
        this.config.formButtonConfigs.push(btnConfig);
      }
    }
    this.config.formFieldConfigs = new Array<FormFieldConfig>();
    this.getFieldConfigs(this.fieldConfigs);

    this.config.formMethodConfigs = this.operationConfig.operations || new Array();
  }

  private getFieldConfigs(fieldConfigs: Array<any>) {
    if (fieldConfigs && fieldConfigs.length > 0) {
      for (const f of fieldConfigs) {
        if (f.children && f.children.length > 0) {
          this.getFieldConfigs(f.children);
        }
        if (f.leaf === false) {
          continue;
        }
        const field = this.formatFieldList.find(fd => fd.fieldId === f.data.fieldId);
        const fieldConfig = new FormFieldConfig();
        fieldConfig.fieldId = f.data.fieldId;
        if (f.data.editable) {
          fieldConfig.fieldState = 'Editable';
        } else if (f.data.readonly) {
          fieldConfig.fieldState = 'Readonly';
        } else if (f.data.hidden) {
          fieldConfig.fieldState = 'Hidden';
        } else if (f.data.required) {
          fieldConfig.fieldState = 'Required';
        } else {
          continue;
        }

        if (!field.fieldState && fieldConfig.fieldState) {
          this.config.formFieldConfigs.push(fieldConfig);
        }

        if (field.fieldState && field.fieldState !== fieldConfig.fieldState) {
          this.config.formFieldConfigs.push(fieldConfig);
        }
      }
    }
  }

  private getConfig() {
    if (this.formFormatId) {
      forkJoin(this.getFormFormat(), this.getFormFormatConfig()).subscribe((re: any[]) => {
        const formFormat = re[0] as FormFormat;
        if (formFormat) {
          this.formFormat = this.convertFormFormat(formFormat);
        }
        const config = re[1] as FormFormatConfig;
        if (config) {
          this.config = config;
        }
        if (this.formFormat.formButtons) {
          this.buttonConfigs = this.formFormat.formButtons.map(b => Object.assign({}, b, {
            enable: b.buttonState === 'Enable' ? true : false,
            disabled: b.buttonState === 'Disabled' ? true : false,
            hidden: b.buttonState === 'Hidden' ? true : false
          }));
          let btnConfigs = new Array<FormButtonConfig>();
          if (config && config.formButtonConfigs) {
            btnConfigs = config.formButtonConfigs;
          }
          for (const b of btnConfigs) {
            for (const btn of this.buttonConfigs) {
              if (btn.buttonId === b.buttonId) {
                switch (b.buttonState) {
                  case 'Enable':
                    btn.enable = true;
                    btn.disabled = false;
                    btn.hidden = false;
                    break;
                  case 'Disabled':
                    btn.enable = false;
                    btn.disabled = true;
                    btn.hidden = false;
                    break;
                  case 'Hidden':
                    btn.enable = false;
                    btn.disabled = false;
                    btn.hidden = true;
                    break;
                }
                break;
              }
            }
          }
        }
        if (this.formFormat.formFields) {
          this.fieldConfigs = [...this.formFormat.formFields];
          this.orgFieldConfigs(this.fieldConfigs);
          let configs = new Array<FormFieldConfig>();
          if (config && config.formFieldConfigs) {
            configs = config.formFieldConfigs;
          }
          for (const f of configs) {
            this.setFieldConfig(this.fieldConfigs, f);
          }
        }
      });
    }
  }

  private orgFieldConfigs(formFields: Array<FormFieldData>) {
    if (formFields.length > 0) {
      for (const d of formFields) {
        d.data = Object.assign({}, d.data, {
          editable: d.data.fieldState === 'Editable' ? true : false,
          readonly: d.data.fieldState === 'Readonly' ? true : false,
          hidden: d.data.fieldState === 'Hidden' ? true : false,
          required: d.data.fieldState === 'Required' ? true : false
        });
        if (d.children && d.children) {
          this.orgFieldConfigs(d.children);
        }
      }
    }
  }

  private setFieldConfig(fieldConfigs: Array<any>, config: FormFieldConfig) {
    if (fieldConfigs.length > 0) {
      for (const f of fieldConfigs) {
        if (f.data.fieldId === config.fieldId) {
          switch (config.fieldState) {
            case 'Editable':
              f.data.editable = true;
              f.data.readonly = false;
              f.data.hidden = false;
              f.data.required = false;
              break;
            case 'Readonly':
              f.data.editable = false;
              f.data.readonly = true;
              f.data.hidden = false;
              f.data.required = false;
              break;
            case 'Hidden':
              f.data.editable = false;
              f.data.readonly = false;
              f.data.hidden = true;
              f.data.required = false;
              break;
            case 'Required':
              f.data.editable = false;
              f.data.readonly = false;
              f.data.hidden = false;
              f.data.required = true;
              break;
          }
          return;
        } else {
          if (f.children && f.children.length > 0) {
            this.setFieldConfig(f.children, config);
          }
        }
      }
    }
  }

  private saveConfig(config: FormFormatConfig): Observable<any> {
    const url = '/api/runtime/wf/v1.0/formFormatConfig';
    return this.http.post(url, config);
  }

  private getFormFormat(): Observable<any> {
    if (this.formFormatId) {
      let url = `/api/runtime/wf/v1.0/formFormat/${this.formFormatId}`;
      url = encodeURI(url);
      return this.http.get(url);
    } else {
      return of(null);
    }
  }

  private getFormFormatConfig(): Observable<any> {
    if (this.formatConfigId) {
      let url = `/api/runtime/wf/v1.0/formFormatConfig/${this.formatConfigId}`;
      url = encodeURI(url);
      return this.http.get(url);
    } else {
      return of(null);
    }
  }

  private convertFormFormat(formFormat: FormFormat): FormFormat {
    if (!formFormat.status) {
      formFormat.status = 0;
    }
    this.convertformFields(formFormat.formFields);
    this.getFormatFieldList(formFormat.formFields, this.formatFieldList);
    return formFormat;
  }

  private convertformFields(formFields: Array<FormFieldData>) {
    if (formFields.length > 0) {
      for (const d of formFields) {
        if (typeof(d.leaf) !== 'boolean' && (d.leaf == null || d.leaf === undefined)) {
          d.leaf = true;
        }
        if (d.children && d.children) {
          this.orgFieldConfigs(d.children);
        }
      }
    }
  }

  private getFormatFieldList(formFields: Array<FormFieldData>, array: Array<FormField>) {
    if (formFields.length > 0) {
      for (const d of formFields) {
        array.push(d.data);
        if (d.children && d.children) {
          this.getFormatFieldList(d.children, array);
        }
      }
    }
  }

  private getFormatField(formFields: Array<FormFieldData>, fieldId: string): FormFieldData {
    for (const f of formFields) {
      if (f.data.fieldId === fieldId) {
        return f;
      }
      if (f.children && f.children.length > 0) {
        return this.getFormatField(f.children, fieldId);
      }
    }

  }

}
