import { Component, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { AutoHandle, ExpireOperation } from "../time-limit-control.entity";
import { MessagerService } from '@farris/ui-messager';
import { ProcessDesignerUIState } from '../../../app/process-designer.uistate';

@Component({
  selector: 'lib-auto-handle-operation',
  templateUrl: './auto-handle-operation.component.html',
  styleUrls: ['./auto-handle-operation.component.css']
})
export class AutoHandleOperationComponent implements OnInit {

  @ViewChild('footer') footer: TemplateRef<any>;
  @Input()
  set value(info: AutoHandle) {
    this.operation = Object.assign({}, info);
    if (this.operation.autoHandleType === undefined) {
      const autoHandle = new AutoHandle(this.autoHandleType[0].value);
      this.operation.autoHandleType = autoHandle.autoHandleType;
      this.operation.message = autoHandle.message;
    }
  }

  get value() {
    return this.operation;
  }

  placeholder = '';
  operation: AutoHandle = new AutoHandle('Pass');
  @Output() submitEmitter = new EventEmitter<ExpireOperation>();
  @Output() closeEmitter = new EventEmitter<any>();

  autoHandleType = [
    // { value: 'autoPass', text: 'èªå¨éè¿' },
    // { value: 'autoBack', text: 'èªå¨é©³å' }
  ];

  constructor(
    private message: MessagerService,
    public uiState: ProcessDesignerUIState,
  ) {
    for(const item of this.uiState.propertyData.ApprovalItems) {
      this.autoHandleType.push({value: item.Value,text: item.Text});
    }
  }

  ngOnInit() {
  }

  clickOK() {
    if (this.validator()) {
      this.submitEmitter.emit(this.operation);
    }
  }

  clickCancel() {
    this.closeEmitter.emit();
  }

  private validator(): boolean {
    if (!this.operation.message) {
      this.message.info('èªå¨åçæè§ä¸åè®¸ä¸ºç©ºï¼');
      return false;
    }
    if (!this.operation.autoHandleType) {
      this.message.info('èªå¨åçä¸åè®¸ä¸ºç©ºï¼');
      return false;
    }
    return true;
  }

}
