import { Component, OnInit, HostBinding, EventEmitter, Output, Input, ViewChild, TemplateRef, ComponentFactoryResolver, Injector } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { activitySendTime, MessageNotice, sendTypes, processSendTime } from './message-notice';
import { BsModalService } from '@farris/ui-modal';
import { MessagerService } from '@farris/ui-messager';
import { MessageNoticeEditComponent } from './message-notice-edit/message-notice-edit.component';
import { DatagridComponent } from '@farris/ui-datagrid';
import { ProcessDeUtil } from '../../domain/process-de-util';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';

@Component({
  selector: 'lib-message-notice',
  templateUrl: './message-notice.component.html',
  styleUrls: ['./message-notice.component.scss']
})
export class MessageNoticeComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';

  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input()
  set value(notices: any[]) {
    if (notices && notices.length > 0) {
      this.messageNotices = notices.map(n => ({
        id: ProcessDeUtil.GenerateElementId(),
        content: n.content,
        sendTime: n.sendTime,
        sendType: n.sendType,
        subject: n.subject,
        receiver: n.receiver,
        receiverText: n.receiverText
      }));
    }
  }
  @Input() editorParams: any = {};
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  @ViewChild('grid') grid: DatagridComponent;

  modalConfig = {
    title: 'æ¶æ¯éç¥',
    width: 800,
    height: 515,
    showButtons: true
  };

  messageNotices: Array<MessageNotice> = new Array();
  columns = [];

  location: 'activity' | 'process' | 'messageActivity' = 'activity';

  constructor(
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private modalService: BsModalService,
    private message: MessagerService,
    private uistate: ProcessDesignerUIState
  ) { }

  ngOnInit() {
    this.location = this.editorParams.location;
    this.columns = [
      {
        field: 'subject', width: 100, title: 'æ¶æ¯ä¸»é¢',
      }, {
        field: 'content', width: 150, title: 'æ¶æ¯åå®¹', showTips: true,
      }, {
        field: 'sendTime', width: 100, title: 'åéæ¶æº',
        formatter: {
          type: 'enum',
          options: { valueField: 'value', textField: 'text', data: this.location === 'process' ? processSendTime : activitySendTime }
        },
      }, {
        field: 'sendType', width: 100, title: 'åéæ¹å¼',
        formatter: {
          type: 'enum',
          options: { valueField: 'value', textField: 'name', data: this.uistate.messageChannels.map(m => ({ value: m.code, name: m.name })) }
        },
      }, {
        field: 'receiverText', width: 120, title: 'æ¶æ¯æ¥æ¶äºº', showTips: true
      }];
    if (this.location === 'messageActivity') {
      this.columns = this.columns.filter(c => c.field !== 'sendTime');
    }
  }

  add() {
    let dialog: any;
    const compFactory = this.resolver.resolveComponentFactory(MessageNoticeEditComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.location = this.location;
    const messageNotice = new MessageNotice();
    if (this.location === 'activity') {
      messageNotice.sendTime = 'WORKITEM_CREATED';
      messageNotice.receiver = 'workItemParticipant';
      messageNotice.receiverText = 'ä»»å¡åçäºº';
    } else if (this.location === 'process') {
      messageNotice.sendTime = 'PROCESSINSTANCE_COMPLETED';
      messageNotice.receiver = 'procInstCreator';
      messageNotice.receiverText = 'æµç¨åèµ·äºº';
    } else if (this.location === 'messageActivity') {
      messageNotice.receiver = 'procInstCreator';
      messageNotice.receiverText = 'æµç¨åèµ·äºº';
    }
    compRef.instance.value = messageNotice;
    compRef.instance.submitEmitter.subscribe((re: MessageNotice) => {
      this.messageNotices = this.messageNotices.concat([re]);
      dialog.close();
    });
    compRef.instance.closeEmitter.subscribe(re => {
      dialog.close();
    });
    const modalConfig = {
      title: 'æ¶æ¯éç¥',
      width: 490,
      height: this.location === 'messageActivity' ? 450 : 490,
      showButtons: true,
      buttons: compRef.instance.footer
    };
    dialog = this.modalService.show(compRef, modalConfig);
  }
  edit() {
    if (!this.grid.selectedRow) {
      this.message.info('å°æªéä¸­æ¶æ¯éç¥ï¼');
      return;
    }
    let dialog: any;
    const compFactory = this.resolver.resolveComponentFactory(MessageNoticeEditComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.location = this.location;
    compRef.instance.value = this.grid.selectedRow.data;
    compRef.instance.submitEmitter.subscribe((re: MessageNotice) => {
      this.messageNotices = this.messageNotices.map(m => {
        if (m.id === re.id) {
          return re;
        }
        return m;
      });
      dialog.close();
    });
    compRef.instance.closeEmitter.subscribe(re => {
      dialog.close();
    });
    const modalConfig = {
      title: 'æ¶æ¯éç¥',
      width: 490,
      height: this.location === 'messageActivity' ? 450 : 490,
      showButtons: true,
      buttons: compRef.instance.footer
    };
    dialog = this.modalService.show(compRef, modalConfig);
  }
  delete() {
    const selected = this.grid.selectedRow;
    if (!selected) {
      this.message.info('å°æªéä¸­æ¶æ¯éç¥ï¼');
      return;
    }
    this.messageNotices = this.messageNotices.filter(m => m.id !== selected.id);
  }
  clickOK() {
    const object = { value: this.messageNotices, parameters: null };
    this.submitModal.emit(object);
  }
  clickCancel() {
    this.closeModal.emit();
  }
}

export class MessageNoticeConverter implements TypeConverter {
  constructor() { }

  convertTo(data: any[]): string {
    if (data && data.length > 0) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
