import { Component, OnInit, HostBinding, Output, EventEmitter, TemplateRef, Input, ViewChild, ComponentFactoryResolver, Injector, AfterViewInit } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { ActivityComponentRepository } from './activity-component.repository';
import { ActivityComponentUIState } from './activity-component.uistate';
import { ActivityComponentViewModel } from './activity-component.viewmodel';
import { FormBuilder } from '@angular/forms';
import { BizComponentEntity } from '../../domain/entities/biz-component.entity';
import { ActualParameter } from '../../domain/entities/actual-parameter';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';

import { ExprEditMode, ExpressionService, InjectedType, VarEntity } from '@gsp-svc/expression';
import { SelectMetadataRTComponent, Metadata4RefDto, MetadataDto } from '@gsp-lcm/metadatart-selector';
import { SessionService } from '@ecp-caf/caf-common';
import { BsModalService } from '@farris/ui-modal';
import { SelectorResult, CommonComponentUiRTService } from '@gsp-cmp/ccmp-ui';
import { GspComponent } from '@gsp-cmp/common-component';
import { DataColumn, DatagridComponent } from '@farris/ui-datagrid';
import { of } from 'rxjs';
import { EditorTypes } from '@farris/ui-datagrid-editors';
import { MessagerService } from '@farris/ui-messager';

@Component({
  selector: 'app-activity-component',
  templateUrl: './activity-component.component.html',
  styleUrls: ['./activity-component.component.scss'],
  providers: [ActivityComponentRepository, ActivityComponentUIState, ActivityComponentViewModel]
})
export class ActivityComponentComponent implements OnInit, AfterViewInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input() editorParams: any = {};
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  modalConfig = {
    title: 'æä»¶',
    width: 1000,
    height: 530,
    showButtons: true
  };
  @Input()
  set value(cpts: any[]) {
    if (cpts && cpts.length > 0) {
      for (const cpt of cpts) {
        const bizComponent = new BizComponentEntity(cpt.Name || cpt.name, cpt.MetadataId || cpt.metadataId, cpt.MethodCode || cpt.methodCode);
        bizComponent.executionTime = cpt.Time || cpt.executionTime,
        bizComponent.id = cpt.Id || cpt.id;
        bizComponent.isForCompensation = cpt.isForCompensation;
        bizComponent.actualParameters = [];
        if ((cpt.ActualParameters && cpt.ActualParameters.length > 0) || (cpt.actualParameters && cpt.actualParameters.length > 0)) {
          const actualParameters = cpt.ActualParameters || cpt.actualParameters;
          for (const para of actualParameters) {
            const actualParameter = new ActualParameter(para.Name || para.code, para.Name || para.name, para.Value || para.value );

            bizComponent.actualParameters.push(actualParameter);
          }
        }
        this.uistate.components.push(bizComponent);
      }
    }
  }

  @ViewChild('cmplist') cmplist: DatagridComponent;
  @ViewChild('paramlist') paramlist: DatagridComponent;


  location: 'activity' | 'process' | 'autoActivity';
  columns = [];
  paramColumns = [];
  constructor(
    public uistate: ActivityComponentUIState,
    public viewModel: ActivityComponentViewModel,
    private formBuilder: FormBuilder,
    private designerState: ProcessDesignerUIState,
    private expr: ExpressionService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private sessionSrc: SessionService,
    private modalService: BsModalService,
    private componentService: CommonComponentUiRTService,
    private repository: ActivityComponentRepository,
    private message: MessagerService
  ) { }

  ngOnInit() {
    this.location = this.editorParams.location;
    if (this.location === 'activity') {
      this.uistate.executionCollection = this.uistate.activityCollection;
      this.columns = [
        {
          field: 'name', width: 100, title: 'æä»¶åç§°'
        },
        { field: 'metadataId', width: 100, title: 'æä»¶ID' },
        { field: 'methodCode', width: 100, title: 'æä»¶æ¹æ³' },
        {
          field: 'executionTime', width: 100, title: 'æ§è¡æ¶æº',
          editor: {
            type: EditorTypes.COMBOLIST,
            options: { data: this.uistate.executionCollection, valueField: 'value', textField: 'text', idField: 'value', panelHeight: 'auto' }
          },
          formatter: (v, d, i) => {
            if (v) {
              return this.uistate.executionCollection.find(item => item.value === v).text;
            } else {
              return '';
            }
          },
          validators: [
            { type: 'required', messager: 'å¿å¡«' }
          ]
        },
        {
          field: 'isForCompensation', width: 50, title: 'è¡¥å¿æä½',
          editor: { type: EditorTypes.SWITCH, options: {} },
          formatter: { type: 'boolean', options: { trueText: 'æ¯', falseText: 'å¦' } }
        }
      ];
    } else if (this.location === 'autoActivity') {
      this.columns = [
        {
          field: 'name', width: 100, title: 'æä»¶åç§°'
        },
        { field: 'metadataId', width: 100, title: 'æä»¶ID' },
        { field: 'methodCode', width: 100, title: 'æä»¶æ¹æ³' },
        {
          field: 'isForCompensation', width: 50, title: 'è¡¥å¿æä½',
          editor: { type: EditorTypes.SWITCH, options: {} },
          formatter: { type: 'boolean', options: { trueText: 'æ¯', falseText: 'å¦' } }
        }
      ];
    } else {
      this.columns = [
        {
          field: 'name', width: 100, title: 'æä»¶åç§°'
        },
        { field: 'metadataId', width: 100, title: 'æä»¶ID' },
        { field: 'methodCode', width: 100, title: 'æä»¶æ¹æ³' },
        {
          field: 'executionTime', width: 100, title: 'æ§è¡æ¶æº',
          editor: {
            type: EditorTypes.COMBOLIST,
            options: { data: this.uistate.executionCollection, valueField: 'value', textField: 'text', idField: 'value', panelHeight: 'auto' }
          },
          formatter: (v, d, i) => {
            if (v) {
              return this.uistate.executionCollection.find(item => item.value === v).text;
            } else {
              return '';
            }
          },
          validators: [
            { type: 'required', messager: 'å¿å¡«' }
          ]
        }
      ];
    }
    if (this.uistate.components && this.uistate.components.length > 0) {
      this.cmplist.selectRow(this.uistate.components[0].id, true);
    }

    this.paramColumns = [
      { field: 'code', width: 50, title: 'ç¼å·' },
      { field: 'name', width: 50, title: 'åç§°' },
      {
        field: 'value', width: 100, title: 'åæ°å¼',
        editor: {
          type: 'input-group',
          options: {
            groupText: '<i class="f-icon f-icon-lookup"></i>',
            editable: false,
            clickHandle: (e) => {
              e.formControl.setValue(e.instance.value ? e.instance.value : '');
              this.showExprHelp(e);
            },
            clear: (e) => {
              console.log('clear', e);
            }
          }
        }
      }
    ];
    // éä¸­å½åè¡
    this.repository.subject.subscribe(cmp => {
      this.cmplist.loadData(this.uistate.components);
      if (cmp) {
        this.cmplist.selectRow(cmp.id, true);
      } else {
        this.uistate.curComponent = null;
      }
    });
  }

  ngAfterViewInit() {
    if (this.uistate.components && this.uistate.components.length > 0) {
      this.cmplist.selectRow(this.uistate.components[0].id, true);
    }
  }

  deleteRow() {
    this.closeCellEdit();

    this.viewModel.deleteRow();
  }

  moveUp() {
    if (!this.uistate.curComponent) {
      this.message.info('å°æªéä¸­æä»¶ï¼');
      return;
    }
    const curCmpId: string = this.uistate.curComponent.id;
    const i = this.uistate.components.findIndex(c => c.id === curCmpId);
    if (i === 0) {
      return;
    }
    this.repository.swapArray(this.uistate.components, i, i - 1);

    this.cmplist.loadData(this.uistate.components);
    this.cmplist.selectRow(curCmpId, true);
  }

  moveDown() {
    if (!this.uistate.curComponent) {
      this.message.info('å°æªéä¸­æä»¶ï¼');
      return;
    }
    const curCmpId: string = this.uistate.curComponent.id;
    const i = this.uistate.components.findIndex(c => c.id === curCmpId);
    if (i === this.uistate.components.length - 1) {
      return;
    }
    this.repository.swapArray(this.uistate.components, i, i + 1);

    this.cmplist.loadData(this.uistate.components);
    this.cmplist.selectRow(curCmpId, true);
  }

  clickOK() {
    this.closeCellEdit();

    if (this.validator()) {
      const object = { value: this.uistate.components, parameters: null };
      this.submitModal.emit(object);
    }
  }

  clickCancel() {
    this.closeModal.emit();
  }
  componentSelect() {
    const compFactory = this.resolver.resolveComponentFactory(SelectMetadataRTComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.typeList = 'WebServiceComponent'; // ç®ååªè½æ ¹æ®åæ°æ®ç±»åè¿æ»¤ï¼åç»­æ·»å æ ç­¾è¿æ»¤
    compRef.instance.selectedMetadata.subscribe(($event: Metadata4RefDto) => {
      try {
        const meta: MetadataDto = $event.metadata;
        this.openComponentSelectorNew(meta.id, this.sessionSrc.getUserSessionId());
      } catch {
        console.log('$event', $event);
        throw new Error('è·åè§£ææä»¶åºé' + $event);
      }
    });

    const modalConfig = {
      title: 'æä»¶éæ©',
      width: 980,
      height: 548,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => dialog.close());
  }
  /**
   * åæ°æä»¶å¸®å©new
   */
  openComponentSelectorNew(metadataId: string, sessionId: string) {
    this.componentService.openSelector(metadataId, sessionId).subscribe(
      (selectorResult: SelectorResult) => {
        selectorResult.confirmEvent.subscribe(
          (result: GspComponent) => {
            this.viewModel.componentSelect(result);
          });
      });
  }
  showExprHelp(e: any) {
    if (this.designerState.schemas.length > 0) {
      for (const schema of this.designerState.schemas) {
        this.expr.addSchema('Schema', schema);
      }
      if (this.designerState.schemas[0].entityTypes && this.designerState.schemas[0].entityTypes.length > 0) {
        const entityType = this.designerState.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array<VarEntity>();
    if (this.designerState.procInstContext.length > 0) {
      for (const variable of this.designerState.procInstContext) {
        context.push(variable);
      }
    }
    if (this.designerState.formalParameterContext.length > 0) {
      for (const variable of this.designerState.formalParameterContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.designerState.participantContext.length > 0) {
      for (const variable of this.designerState.participantContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.location === 'activity') {
      for (const variable of this.designerState.activityComponentContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.designerState.processVariableContext.length > 0) {
      for (const variable of this.designerState.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
    this.expr.setEditPattern(ExprEditMode.Advanced); // é»è®¤æå¼é«çº§è¡¨è¾¾å¼ç¼è¾å¨
    this.expr.addExpressionText(e.instance.value);
    this.expr.buildExpression().then((expressioninfo: string) => {
      e.formControl.setValue(expressioninfo);
      e.instance.cd.detectChanges();
    });
  }
  selectionChange(data: any) {
    this.uistate.curComponent = data.data;
  }
  paramAfterEdit = (rowIndex: number, rowData: any, column?: DataColumn) => {
    return of(true);
  }

  private closeCellEdit() {
    this.cmplist.endCellEdit();
    if (this.paramlist) {
      this.paramlist.endCellEdit();
    }
  }

  private validator(): boolean {
    for (const c of this.uistate.components) {
      if (!c.executionTime && this.location !== 'autoActivity') {
        this.message.warning('æä»¶æ§è¡æ¶æºä¸åè®¸ä¸ºç©ºï¼');
        return false;
      }
    }
    return true;
  }
}

export class ActivityComponentConverter implements TypeConverter {
  constructor() { }

  convertTo(data: any[]): string {
    if (data && data.length > 0) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
