import { Component, OnInit, HostBinding, Output, EventEmitter, Input, ViewChild, TemplateRef, ComponentFactoryResolver, Injector, AfterViewInit } from '@angular/core';
import { CustomizeAciton } from './entities/customize-action.entity';
import { DatagridComponent } from '@farris/ui-datagrid';
import { EditorTypes } from '@farris/ui-datagrid-editors';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';
import { ExpressionService, InjectedType } from '@gsp-svc/expression';
import { BsModalService, ModalOptions } from '@farris/ui-modal';
import { CustomizeActionListComponent } from './views/customize-action-list/customize-action-list.component';
import { TypeConverter } from '@farris/ui-property-panel';
import { MessagerService } from '@farris/ui-messager';
import { ActualParameter } from '../execute-forms/entities/execute-form.entity';

@Component({
  selector: 'app-customize-action',
  templateUrl: './customize-action.component.html',
  styleUrls: ['./customize-action.component.scss']
})
export class CustomizeActionComponent implements OnInit, AfterViewInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @Input() editorParams: any = {};
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  modalConfig = {
    title: 'èªå®ä¹å¨ä½',
    width: 800,
    height: 520,
    showButtons: true
  };
  @Input() set value(data: any[]) {
    if (data && data.length > 0) {
      for (const d of data) {
        const action = new CustomizeAciton(d.id, d.code, d.name);
        action.terminalType = d.terminalType;
        action.actualParameters = [];
        if (d.actualParameters && d.actualParameters.length > 0) {
          for (const a of d.actualParameters) {
            const p = new ActualParameter();
            p.Name = a.Name;
            p.Value = a.Value;
            p.ValueText = a.ValueText;
            p.Tag = a.Tag;
            action.actualParameters.push(p);
          }
        }
        this.actions.push(action);
      }
    }
  }

  @ViewChild('actionlist') actionlist: DatagridComponent;
  @ViewChild('paramlist') paramlist: DatagridComponent;

  private terminalList = [
    { value: 'app', text: 'ç§»å¨' },
    { value: 'web', text: 'web' },
    { value: 'web,app', text: 'web,ç§»å¨' }
  ];
  actions: CustomizeAciton[] = [];
  curAction: CustomizeAciton;
  columns = [
    { field: 'code', width: 100, title: 'ç¼å·' },
    { field: 'name', width: 100, title: 'åç§°' },
    {
      field: 'terminalType', width: 100, title: 'éç¨ç»ç«¯',
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: this.terminalList }
      },
    },
    // {
    //   field: 'enable', width: 100, title: 'æ¯å¦å¯ç¨',
    //   editor: { type: EditorTypes.SWITCH, options: {} },
    //   formatter: { type: 'boolean', options: { trueText: 'æ¯', falseText: 'å¦' } }
    // }
  ];
  paramColumns = [
    { field: 'Name', width: 50, title: 'ç¼å·' },
    { field: 'ValueText', width: 50, title: 'åç§°' },
    {
      field: 'Value', width: 100, title: 'åæ°å¼',
      editor: {
        type: 'input-group',
        options: {
          groupText: '<i class="f-icon f-icon-lookup"></i>',
          editable: false,
          clickHandle: (e) => {
            e.formControl.setValue(e.instance.value ? e.instance.value : '');
            this.showExprHelp(e);
          },
          clear: (e) => {
            console.log('clear', e);
          }
        }
      }
    }
  ];
  constructor(
    private designerState: ProcessDesignerUIState,
    private expr: ExpressionService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private modalService: BsModalService,
    private message: MessagerService
  ) { }

  ngOnInit() {

  }
  ngAfterViewInit() {
    this.loadData();
    // this.actionlist.loadData(this.actions);
    // if (this.actions && this.actions.length > 0) {
    //   this.actionlist.selectRow(this.actions[0].id, true);
    // }
  }
  addActions() {
    const compFactory = this.resolver.resolveComponentFactory(CustomizeActionListComponent);
    const compRef = compFactory.create(this.injector);
    const modalConfig: ModalOptions = {
      title: 'èªå®ä¹å¨ä½éæ©',
      width: 800,
      height: 520,
      showButtons: true,
      buttons: compRef.instance.footer
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.submit.subscribe((data: CustomizeAciton[]) => {
      if (data && data.length > 0) {
        for (const a of data) {
          const index = this.actions.findIndex(act => act.code === a.code);
          if (index > -1) {
            this.actions.splice(index, 1);
          }
          this.actions.push(a);
        }
        this.loadData();
      }
      dialog.close();
    });
    compRef.instance.cancel.subscribe($event => dialog.close());
  }
  delete() {
    this.actionlist.endCellEdit();
    if (this.actionlist.selectedRow && this.actionlist.selectedRow.data) {
      const i = this.actions.findIndex(d => d.code === this.actionlist.selectedRow.data.code);
      this.actions.splice(i, 1);
      this.loadData();
      // this.actionlist.loadData(this.actions);
      // if (this.actions && this.actions.length > 0) {
      //   this.actionlist.selectRow(this.actions[0].id, true);
      // } else {
      //   this.curAction = null;
      // }
    } else {
      this.message.info('è¯·é¦åéä¸­ä¸æ¡éé¡¹ï¼');
    }
  }

  clickOK() {
    this.actionlist.endCellEdit();
    this.paramlist && this.paramlist.endCellEdit();

    const object = { value: this.actions, parameters: null };
    this.submitModal.emit(object);
  }

  clickCancel() {
    this.closeModal.emit();
  }
  loadData() {
    this.actionlist.loadData(this.actions);
    if (this.actions.length > 0 && (!this.actionlist.selectedRow || !this.actionlist.selectedRow.data)) {
      this.actionlist.selectRow(this.actions[0].id, true);
    } else {
      this.curAction = null;
    }
  }

  selectChanged(data: any) {
    this.curAction = data.data;
  }

  showExprHelp(e: any) {
    if (this.designerState.schemas.length > 0) {
      for (const schema of this.designerState.schemas) {
        this.expr.addSchema('Schema', schema);
      }
      if (this.designerState.schemas[0].entityTypes && this.designerState.schemas[0].entityTypes.length > 0) {
        const entityType = this.designerState.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array();
    if (this.designerState.procInstContext.length > 0) {
      for (const variable of this.designerState.procInstContext) {
        context.push(variable);
        // this.expr.addContext(variable);
      }
    }
    if (this.designerState.formalParameterContext.length > 0) {
      for (const variable of this.designerState.formalParameterContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
        // this.expr.addContext(variable);
      }
    }
    if (this.designerState.participantContext.length > 0) {
      for (const variable of this.designerState.participantContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
        // this.expr.addContext(variable);
      }
    }
    if (this.designerState.processVariableContext.length > 0) {
      for (const variable of this.designerState.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
        // this.expr.addContext(variable);
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
    // if (e.instance.value) {
    this.expr.addExpressionText(e.instance.value);
    // }
    this.expr.buildExpression().then((expressioninfo: string) => {
      e.formControl.setValue(expressioninfo);
      e.instance.cd.detectChanges();
    });
  }
}

export class CustomizeActionConverter implements TypeConverter {
  constructor() { }

  convertTo(data: any[]): string {
    if (data && data.length > 0) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
