import { Component, OnInit, Output, EventEmitter, ViewChild, TemplateRef, Input } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { UrgeHandleRule } from './urge-handle-rule';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';
import {ExpressionService, InjectedType} from '@gsp-svc/expression';
import { MessagerService } from '@farris/ui-messager';

@Component({
  selector: 'lib-urge-handle-rule',
  templateUrl: './urge-handle-rule.component.html',
  styleUrls: ['./urge-handle-rule.component.scss']
})
export class UrgeHandleRuleComponent implements OnInit {

  groupIcon2 = '<i class="f-icon f-icon-lookup"></i>';
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;

  @Input()
  set value(data: any) {
    if (!data) {
      this.urgeHandleRule.urgeHandleAble = true;
      this.urgeHandleRule.sendType = 'cloudplus,inmail';
    } else {
      this.urgeHandleRule = data;
    }
  }

  urgeHandleRule: UrgeHandleRule = new UrgeHandleRule();


  typeGroup = this.designerUIState.messageChannels.map(m => ({ value: m.code, name: m.name }));

  modalConfig = {
    title: 'å¬å',
    width: 500,
    height: 445,
    showButtons: true
  };

  constructor(
    private designerUIState: ProcessDesignerUIState,
    private expr: ExpressionService,
    private message: MessagerService) {}

  ngOnInit() {
  }

  clickOK() {
    if (this.validator()) {
      const object = { value: this.urgeHandleRule, parameters: null };
      this.submitModal.emit(object);
    }
  }

  clickCancel() {
    this.closeModal.emit();
  }

  private validator() {
    if (!this.urgeHandleRule.urgeHandleAble) {
      return true;
    }
    if (!this.urgeHandleRule.sendType) {
      this.message.info('å°æªéæ©åéæ¹å¼ï¼');
      return false;
    }
    if (!this.urgeHandleRule.urgeMessageSubject) {
      this.message.info('å°æªéç½®æ¶æ¯ä¸»é¢ï¼');
      return false;
    }
    if (!this.urgeHandleRule.urgeMessageContent) {
      this.message.info('å°æªéæ©æ¶æ¯åå®¹ï¼');
      return false;
    }
    return true;
  }

  parser = (val: string | number) => {
    return Number(val);
  }

  titleExpr(data: any){
    this.showTitleHelp(data);
  }

  contentExpr(data: any){
    this.showContentHelp(data);
  }

  showTitleHelp(data: string) {
    this.showExpr();
    if (this.urgeHandleRule.urgeMessageSubject && this.urgeHandleRule.urgeMessageSubject.length > 0) {
      this.expr.addExpressionText(this.urgeHandleRule.urgeMessageSubject);
    } else {
      this.expr.addExpressionText('');
    }
    this.expr.buildExpression().then((expressionInfo: string) => {
      this.urgeHandleRule.urgeMessageSubject = expressionInfo;
    });
  }

  showContentHelp(data: string){
    this.showExpr();
    if (this.urgeHandleRule.urgeMessageContent && this.urgeHandleRule.urgeMessageContent.length > 0) {
      this.expr.addExpressionText(this.urgeHandleRule.urgeMessageContent);
    } else {
      this.expr.addExpressionText('');
    }
    this.expr.buildExpression().then((expressionInfo: string) => {
      this.urgeHandleRule.urgeMessageContent = expressionInfo;
    });
  }

  showExpr(){
    if (this.designerUIState.schemas.length > 0) {
      for (const schema of this.designerUIState.schemas) {
        // this.expr.addCommonObject(co);
        this.expr.addSchema('Schema', schema);
      }
      if (this.designerUIState.schemas[0].entityTypes && this.designerUIState.schemas[0].entityTypes.length > 0) {
        const entityType = this.designerUIState.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array();
    if (this.designerUIState.urgeContext.length > 0) {
      for (const variable of this.designerUIState.urgeContext) {
        context.push(variable);
        // this.expr.addContext(variable);
      }
    }
    if (this.designerUIState.processVariableContext.length > 0) {
      for (const variable of this.designerUIState.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
        // this.expr.addContext(variable);
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
  }

}

export class UrgeHandleRuleConverter implements TypeConverter {
  constructor() { }

  convertTo(data: any): string {
    if (!data) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else if (data.urgeHandleAble) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
