import { Component, OnInit, HostBinding, Output, EventEmitter, Input, ViewChild, TemplateRef, AfterViewInit } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';
import { MessagerService } from '@farris/ui-messager';
import { Observable, of } from 'rxjs';
import { PickedResult, LookupGridComponent } from '@farris/ui-lookup';
import { THROW_IF_NOT_FOUND } from '@angular/core/src/di/injector';

@Component({
  selector: 'procDesigner-backward-rule',
  templateUrl: './backward-rule.component.html',
  styleUrls: ['./backward-rule.component.scss']
})
export class BackwardRuleComponent implements OnInit, AfterViewInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';
  @Input() editorParams: any = {};
  @Input()
  set value(data: any) {
    if (data) {
      this.selectAbleChecked = data.SelectTargetAble;
      this.waitReturnChecked = data.WaitReturn;
      this.dynamicBackWardRuleAbleChecked = data.DynamicBackWardRuleAble;
      if (!data.TargetRef) {
        this.targetRef = 'Empty';
      } else {
        this.targetRef = data.TargetRef;
      }
      const act = this.listItems.find(item => item.Id === this.targetRef);
      if (act) {
        this.displayText = act.Name;
      } else {
        this.displayText = '';
        setTimeout(() => {
          this.message.warning('é©³åæ´»å¨èç¹å·²ä¸å­å¨ï¼è¯·éæ°ç¼è¾');
        }, 500);
      }
    }
  }
  @Input() nodeType: 'UserActivity' | 'CallActivitySubProcess' = 'UserActivity';

  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();

  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  @ViewChild('hlp') hlp: LookupGridComponent;
  modalConfig = {
    title: 'é©³åè§å',
    width: 430,
    height: 370,
    showButtons: true
  };
  displayText: string;
  listItems = [
    { Id: 'InitialActityId', Name: 'å¼å§èç¹' },
    { Id: 'PrecursorActivityId', Name: 'åç½®èç¹' },
    { Id: 'Empty', Name: 'ç©º' }
  ];

  items = [];

  columns = [
    { title: 'åç§°', field: 'Name', width: 100, searchField: 'Name' },
    { title: 'æ è¯', field: 'Id', width: 100, searchField: 'Id' },
  ];
  selectAbleChecked = false;
  waitReturnChecked = false;
  dynamicBackWardRuleAbleChecked = false;
  targetRef = this.listItems[0].Id;

  pageInfo = {
    pageIndex: 1,
    pageList: [10, 20, 30, 50],
    pageSize: 20
  };

  total: number;

  constructor(
    private uiState: ProcessDesignerUIState,
    private message: MessagerService
  ) {
    this.listItems = this.listItems.concat(this.uiState.userActList);
    this.items = this.listItems;
    this.total = this.listItems.length;
    }

  ngOnInit() {
    this.nodeType = this.editorParams.nodeType;
    if (this.nodeType !== 'UserActivity') {
      this.listItems = this.listItems.filter(i => i.Id !== 'Empty');
      this.items = this.listItems;
      this.total = this.listItems.length;
    }
  }

  ngAfterViewInit() {
  }

  selectAbleClick() {
    if (this.uiState.propertyData.ClrTypeID === 'wf.UserActivity'
      && this.uiState.propertyData.CompleteStrategy !== this.uiState.completeIterator[0].key
      && this.selectAbleChecked === true) {
      this.message.info('ä¼ç­¾ç­ç¥ä¸ï¼ä¸åè®¸âå¨æéæ©é©³åçº§æ¬¡â!', () => {
        this.selectAbleChecked = false;
      });
    }
  }
  waitReturnClick() {
    this.waitReturnChecked = !this.waitReturnChecked;
  }
  dynamicBackWardRuleAbleClick() {
    this.dynamicBackWardRuleAbleChecked = !this.dynamicBackWardRuleAbleChecked;
  }
  dictPicked = (arg: any): Observable<PickedResult> => {
    this.targetRef = arg.Id;
    this.displayText = arg.Name;
    return of({ closeDialog: true });
  }
  clickOK() {
    const backWardRule = {
      TargetRef: this.getTargetRef(),
      SelectTargetAble: this.selectAbleChecked,
      WaitReturn: this.waitReturnChecked,
      DynamicBackWardRuleAble: this.dynamicBackWardRuleAbleChecked,
    };
    const object = { value: backWardRule, parameters: null };
    this.submitModal.emit(object);
  }

  clickCancel() {
    this.closeModal.emit();
  }

  pagerChanged(arg: any) {
    this.pageInfo.pageIndex = arg.pageIndex;
    this.pageInfo.pageSize = arg.pageSize;
    const search = {
      field: arg.searchValue.searchField,
      value: arg.searchValue.searchValue
    };
    this.load(search);
  }

  pageSizeChange(arg: any) {
    console.log(arg);
    this.pageInfo.pageIndex = arg.pageIndex;
    this.pageInfo.pageSize = arg.pageSize;
    const search = {
      field: arg.searchValue.searchField,
      value: arg.searchValue.searchValue
    };
    this.load(search);
  }
  search(arg: any) {
    this.pageInfo.pageIndex = arg.pageInfo.pageIndex;
    this.pageInfo.pageSize = arg.pageInfo.pageSize;
    const search = arg.search;

    this.load(search);
  }

  load(search?: { field: string, value: string}) {
    const start = (this.pageInfo.pageIndex - 1) * this.pageInfo.pageSize;
    const end = start + this.pageInfo.pageSize;
    let items = [];
    if (search) {
      if (search.field === '*') {
        items = this.listItems.filter(i => i.Name.indexOf(search.value) > -1 || i.Id.indexOf(search.value) > -1);
      } else {
        items = this.listItems.filter(f => f[search.field].indexOf(search.value) > -1);
      }
    } else {
      items = this.listItems;
    }
    this.total = items.length;
    this.items = items; // .slice(start, end);
    this.hlp.componentRef.instance.loadData({
      // total: this.total,
      // pageSize: this.pageInfo.pageSize,
      // pageIndex: this.pageInfo.pageIndex,
      data: this.items
    });
  }
  private getTargetRef() {
    if (this.targetRef === 'Empty') {
      return '';
    } else {
      return this.targetRef;
    }
  }
}
