import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { map } from 'rxjs/operators';
import { ReplaySubject } from 'rxjs/ReplaySubject';

@Injectable()
export class PerformerConfigService {
    private lookupConfig = new ReplaySubject<any>();
    constructor(private http: HttpClient) {
        this.http.get('/platform/runtime/wf/web/wf-proc-designer/assets/config/performerlookupconfig.json').subscribe((data) => {
            this.lookupConfig.next(data);
        });
    }

    getLookupConfig(): Observable<any> {
        return this.lookupConfig.asObservable();
    }
}
