  @Input()
  set options(options: PerformerOptions) {
    if (options) {
      this.exclusionPerformers = options.ExclusionPerformers;
      this.noPerformerStrategy = options.NoPerformerStrategy;
      this.historicFirst = options.HistoricFirst;
      this.automaticPerformers = options.automaticPerformers;
    }
  }

  @Input()
  set exclusionPerformers(e: string | Array<string>) {
    if (e) {
      if (typeof e === 'string') {
        this.excludeProcInstCreator = e.indexOf('procInstCreator') > -1;
        this.excludePreActExecutors = e.indexOf('preActExecutors') > -1;
      } else {
        this.excludeProcInstCreator = e.findIndex(i => i === 'procInstCreator') > -1;
        this.excludePreActExecutors = e.findIndex(i => i === 'preActExecutors') > -1;
        this.excludeAllPassExecutors = e.findIndex(i => i === 'allPassExecutors') > -1;

        this.excludeExprPerformer = e.find(i => i.indexOf('expr') > -1);
      }
    }
  }

  set automaticPerformers(e: Array<string>) {
    if (e && e.length > 0) {
      this.autoProcInstCreator = e.findIndex(i => i === 'procInstCreator') > -1;
      this.autoPreActExecutors = e.findIndex(i => i === 'preActExecutors') > -1;
      this.autoAllPassExecutors = e.findIndex(i => i === 'allPassExecutors') > -1;
    }

  }

