import { Injectable } from '@angular/core';
import { ProcessDesignerUIState } from './process-designer.uistate';
import { ElementPropertyConfig, PropertyEntity } from '@farris/ui-property-panel';
import { ResourceKeys } from '../model/resourceKeys';
import { ProcessListHelpComponent } from '../aif-process-list-help/components/process-list-help/process-list-help.component';
import { AifCreationBillProcessConverter } from '../aif-process-list-help/AifCreationBillProcessConverter';

@Injectable()
export class AifCreationBillActivityAdapter {
    constructor(private uiState: ProcessDesignerUIState) {

    }
    setProperty(obj: any) {
        this.uiState.propertyData = obj.propertyData;
        this.uiState.propertyConfig = this.setConfig(obj.propertyConfig);
    }

    private setConfig(obj: any[]): ElementPropertyConfig[] {
        const propertyConfig: ElementPropertyConfig[] = [];
        if (obj) {
            obj.forEach((element: ElementPropertyConfig) => {
                this.setEntity(element.properties);
                propertyConfig.push(element);
            });
        }
        return propertyConfig;
    }
    private setEntity(obj: PropertyEntity[]) {
        if (obj && obj.length > 0) {
            obj.forEach((item: PropertyEntity) => {
                switch (item.propertyID) {
                    case ResourceKeys.aifProcessCode: // åæ®æµç¼å·
                        item.editor = ProcessListHelpComponent;
                        item.converter = new AifCreationBillProcessConverter();
                        item.editorParams = {
                            bizTypeId: this.uiState.flowFormInfo.flowForm.businessObjectId
                        };
                        break;
                    default:
                        break;
                }
            });
        }
    }
}
