import { Component, OnInit, HostBinding, EventEmitter, Output, Input, ViewChild, TemplateRef, Injector, ComponentFactoryResolver } from '@angular/core';
import { TypeConverter } from '@farris/ui-property-panel';
import { ProcessVariable } from './process-variable.entity';
import { EditorTypes } from '@farris/ui-datagrid-editors';
import { DatagridComponent } from '@farris/ui-datagrid';
import { MessagerService } from '@farris/ui-messager';
import { BsModalService } from '@farris/ui-modal';
import { ConstantEditComponent } from '../variable-setting/views/constant-edit/constant-edit.component';
import { ProcessDesignerUIState } from '../../app/process-designer.uistate';
import { ExprEditMode, ExpressionService, InjectedType } from '@gsp-svc/expression';
import { ComponentEditComponent } from '../variable-setting/views/component-edit/component-edit.component';
import { RowEditParam, DataColumn } from '@farris/ui-datagrid/lib/types';
import { of } from 'rxjs';

@Component({
  selector: 'lib-process-variables',
  templateUrl: './process-variables.component.html',
  styleUrls: ['./process-variables.component.scss']
})
export class ProcessVariablesComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';

  @Input() variableSetting = false;
  @Output() closeModal = new EventEmitter<any>();
  @Output() submitModal = new EventEmitter<any>();
  @Output() variableSetEmitter = new EventEmitter<any>();

  @Input() value: Array<ProcessVariable> = new Array();
  @Input() editorParams: any = {};
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  @ViewChild('grid') grid: DatagridComponent;

  private basicTypes = [
    { value: 'String', text: 'å­ç¬¦' },
    { value: 'Integer', text: 'æ´å½¢' },
    { value: 'Decimal', text: 'å°æ°' },
    { value: 'Boolean', text: 'å¸å°' },
    // { value: 'DateTime', text: 'æ¥æ' }
  ];

  private tags = [
    { value: 'constant', text: 'å¸¸é' },
    { value: 'expression', text: 'è¡¨è¾¾å¼' },
    { value: 'component', text: 'æä»¶' }
  ];

  columns = [
    {
      field: 'name', width: 80, title: 'åéç¼å·', sortable: true,
      editor: { type: EditorTypes.TEXTBOX, options: {} },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    },
    {
      field: 'documentation', width: 80, title: 'åéåç§°', sortable: true,
      editor: { type: EditorTypes.TEXTBOX, options: {} },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    },
    {
      field: 'basicType', width: 60, title: 'åéç±»å', sortable: true,
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: this.basicTypes }
      },
      editor: {
        type: EditorTypes.COMBOLIST,
        options: { valueField: 'value', textField: 'text', data: this.basicTypes, idField: 'value', panelHeight: 'auto', showClear: false }
      },
      validators: [
        { type: 'required', messager: 'å¿å¡«' }
      ]
    }, {
      field: 'description', width: 100, title: 'æè¿°', showTips: true,
      editor: { type: EditorTypes.TEXTBOX, options: {} },
    }, {
      field: 'tag', width: 60, title: 'åå§å¼ç±»å', sortable: true,
      formatter: {
        type: 'enum',
        options: { valueField: 'value', textField: 'text', data: this.tags }
      },
      editor: {
        type: EditorTypes.COMBOLIST,
        options: {
          valueField: 'value', textField: 'text', data: this.tags, idField: 'value', panelHeight: 'auto',
          selectChange: (e) => {
            this.value = this.value.map(v => {
              if (v.id === e.rowId) {
                v.value = null;
                v.valueText = null;
                if (v.component) {
                  v.component = null;
                }
                return v;
              } else {
                return v;
              }
            });
          },
          clear: (e) => {
            this.value = this.value.map(v => {
              if (v.id === e.rowId) {
                v.value = null;
                v.valueText = null;
                if (v.component) {
                  v.component = null;
                }
                return v;
              } else {
                return v;
              }
            });
          }
        }
      }
    }, {
      field: 'valueText', width: 120, title: 'åå§å¼', showTips: true,
      editor: { type: EditorTypes.TEXTBOX, options: {} }
    }
  ];

  modalConfig = {
    title: 'æµç¨åé',
    width: 1070,
    height: 510,
    showButtons: true
  };

  constructor(
    private message: MessagerService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private modalService: BsModalService,
    private uistate: ProcessDesignerUIState,
    private expr: ExpressionService
  ) { }

  ngOnInit() {
    if (this.variableSetting) {
      this.columns = this.columns.filter(c => c.field !== 'tag'
        && c.field !== 'valueText' && c.field !== 'description'
      );
    }
  }

  add() {
    const variable = new ProcessVariable();
    this.value = this.value.concat([variable]);
  }

  delete() {
    this.grid.endCellEdit();
    const selected = this.grid.selectedRow;
    if (!selected) {
      this.message.info('å°æªéä¸­æµç¨åéï¼');
      return;
    }
    this.value = this.value.filter(v => v.id !== selected.data.id);
  }

  clickOK() {
    if (this.variableSetting) {
      const selecteds = this.grid.checkeds;
      if (selecteds.length > 0) {
        this.variableSetEmitter.emit(selecteds);
      } else {
        this.message.info('å°æªéä¸­æµç¨åéï¼');
      }
    } else {
      this.grid.endCellEdit();
      if (this.validator()) {
        const object = { value: this.value, parameters: null };
        this.submitModal.emit(object);
      }
    }
  }

  clickCancel() {
    this.closeModal.emit();
  }
  beforeEdit = (param: RowEditParam) => {
    const rowData = param.rowData as any;
    const column = param.column;
    if (column.field === 'valueText') {
      if (!(rowData as ProcessVariable).tag) {
        return of(false);
      }
      if ((rowData as ProcessVariable).tag === this.tags[0].value) {
        param.column.editor = { type: EditorTypes.TEXTBOX, options: {} };
      } else {
        param.column.editor = {
          type: 'input-group',
          options: {
            groupText: '<i class="f-icon f-icon-lookup"></i>',
            editable: false,
            showClear: false,
            clickHandle: (e) => {
              e.formControl.setValue(e.instance.value ? e.instance.value : '');
              this.valueEdit(e);
            },
            clear: (e) => {
              console.log('clear', e);
            }
          }
        };
      }
    }
    return of(true);
  }

  endEdit(data: any) {
    if (data.column.field === 'valueText' && data.rowData.tag === this.tags[0].value) {
      data.rowData.value = data.rowData.valueText;
    }
  }
  valueEdit(e: any) {
    const rowdata: ProcessVariable = e.rowData;
    switch (rowdata.tag) {
      case this.tags[0].value:
        this.editConstant(e);
        break;
      case this.tags[1].value:
        this.editExpression(e);
        break;
      case this.tags[2].value:
        this.editComponent(e);
        break;
    }
  }
  private editConstant(e: any) {
    const data: ProcessVariable = e.rowData;
    let dialog: any;
    const compFactory = this.resolver.resolveComponentFactory(ConstantEditComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.value = data.value;
    compRef.instance.submitEmitter.subscribe((re: string) => {
      e.formControl.setValue(re);
      e.instance.cd.detectChanges();
      data.value = re;
      dialog.close();
    });
    compRef.instance.closeEmitter.subscribe(re => {
      dialog.close();
    });
    const modalConfig = {
      title: 'å¸¸é',
      width: 340,
      height: 280,
      showButtons: true,
      buttons: compRef.instance.footer
    };
    dialog = this.modalService.show(compRef, modalConfig);
  }

  private editExpression(e: any) {
    const data: ProcessVariable = e.rowData;
    if (this.uistate.schemas.length > 0) {
      for (const schema of this.uistate.schemas) {
        this.expr.addSchema('Schema', schema);
      }
      if (this.uistate.schemas[0].entityTypes && this.uistate.schemas[0].entityTypes.length > 0) {
        const entityType = this.uistate.schemas[0].entityTypes[0];
        this.expr.addInjectedEntity(entityType.name, 'Schema',
          entityType.name, InjectedType.EntityType);
      }
    }
    const context = new Array();
    if (this.uistate.procInstContext.length > 0) {
      for (const variable of this.uistate.procInstContext) {
        context.push(variable);
      }
    }
    if (this.uistate.formalParameterContext.length > 0) {
      for (const variable of this.uistate.formalParameterContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.uistate.participantContext.length > 0) {
      for (const variable of this.uistate.participantContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (this.uistate.processVariableContext.length > 0) {
      for (const variable of this.uistate.processVariableContext) {
        if (!context.some(c => c.key === variable.key)) {
          context.push(variable);
        }
      }
    }
    if (context.length > 0) {
      for (const v of context) {
        this.expr.addContext(v);
      }
    }
    this.expr.setEditPattern(ExprEditMode.Advanced); // è¡¨è¾¾å¼å±ç°æ¨¡å¼ï¼é»è®¤é«çº§ç
    this.expr.addExpressionText(data.value);
    this.expr.buildExpression().then((re: string) => {
      e.formControl.setValue(re);
      e.instance.cd.detectChanges();
      data.value = re;
    });
  }

  private editComponent(e: any) {
    const data: ProcessVariable = e.rowData;
    let dialog: any;
    const compFactory = this.resolver.resolveComponentFactory(ComponentEditComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.component = data.component;
    compRef.instance.submitEmitter.subscribe((re: any) => {
      e.formControl.setValue(re.Name + '-' + re.MethodCode);
      e.instance.cd.detectChanges();
      data.value = re.Id;
      data.component = re;
      dialog.close();
    });
    compRef.instance.closeEmitter.subscribe(re => {
      dialog.close();
    });
    const modalConfig = {
      title: 'æä»¶',
      width: 800,
      height: 515,
      showButtons: true,
      buttons: compRef.instance.footer
    };
    dialog = this.modalService.show(compRef, modalConfig);
  }

  private validator() {
    for (const item of this.value) {
      if (!item.name) {
        this.message.warning('åéç¼å·ä¸åè®¸ä¸ºç©ºï¼');
        return false;
      }
      if (!item.documentation) {
        this.message.warning('åéåç§°ä¸åè®¸ä¸ºç©ºï¼');
        return false;
      }
      if (!this.IsMatchVariableNameRules(item.name)) {
        this.message.warning('åéç¼å·åªè½åå«å­æ¯ãæ°å­åä¸åçº¿ï¼ä¸ä¸è½ä»¥æ°å­å¼å¤´ï¼');
        return false;
      }
    }
    if (this.value.length > 1) {
      for (let i = 0; i < this.value.length; i++) {
        const code1 = this.value[i].name;
        for (let j = i + 1; j < this.value.length; j++) {
          const code2 = this.value[j].name;

          if (code1 === code2) {
            this.message.warning(`æµç¨åéç¼å·ä¸åè®¸éå¤ï¼`);
            return false;
          }
        }
      }
    }
    return true;
  }
  private IsMatchVariableNameRules(str: string) {
    const Regx = /^[a-zA-Z_][a-zA-Z_0-9]{0,}/;
    if (Regx.test(str)) {
      return true;
    } else {
      return false;
    }
  }
}

export class ProcessVariablesConverter implements TypeConverter {
  constructor() { }

  convertTo(data: any[]): string {
    if (data && data.length > 0) {
      return 'ï¼å·²è®¾ç½®ï¼';
    } else {
      return 'ï¼æªè®¾ç½®ï¼';
    }
  }
}
